import React from "react";
import { StickyNote } from "lucide-react";
import { useState } from "react";
import { Plus, FileText, X } from "lucide-react";

const Notes = ({ balance, notes, setNotes }) => {
  const [activeNoteId, setActiveNoteId] = useState(1);
  const [showNewNoteForm, setShowNewNoteForm] = useState(false);
  const [newNoteTitle, setNewNoteTitle] = useState("");
  const [editingNoteId, setEditingNoteId] = useState(null);
  const [editingTitle, setEditingTitle] = useState("");

  // Получить активную заметку
  const activeNote = notes.find((note) => note.id === activeNoteId) || notes[0];

  // Добавить новую заметку
  const addNote = () => {
    if (newNoteTitle.trim()) {
      const newNote = {
        id: Date.now(),
        title: newNoteTitle,
        content: "",
        type: "text",
      };
      setNotes([...notes, newNote]);
      setActiveNoteId(newNote.id);
      setNewNoteTitle("");
      setShowNewNoteForm(false);
    }
  };

  // Удалить заметку
  const deleteNote = (id) => {
    if (notes.length > 1) {
      const filteredNotes = notes.filter((note) => note.id !== id);
      setNotes(filteredNotes);
      if (activeNoteId === id) {
        setActiveNoteId(filteredNotes[0].id);
      }
    }
  };
  // Начать редактирование заголовка
  const startEditingTitle = (note) => {
    setEditingNoteId(note.id);
    setEditingTitle(note.title);
  };

  // Сохранить отредактированный заголовок
  const saveEditedTitle = () => {
    if (editingTitle.trim()) {
      setNotes(
        notes.map((note) =>
          note.id === editingNoteId
            ? { ...note, title: editingTitle.trim() }
            : note
        )
      );
    }
    setEditingNoteId(null);
    setEditingTitle("");
  };

  // Отменить редактирование
  const cancelEditing = () => {
    setEditingNoteId(null);
    setEditingTitle("");
  };

  // Обновить содержимое заметки
  const updateNoteContent = (content) => {
    setNotes(
      notes.map((note) =>
        note.id === activeNoteId ? { ...note, content } : note
      )
    );
  };

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex justify-between items-center mb-8">
        <h1 className="text-4xl font-light text-white">Notes</h1>
        <div className="text-right">
          <div className="text-sm text-white/60">Balance</div>
          <div
            className={`text-2xl font-semibold ${
              balance >= 0 ? "text-green-400" : "text-red-400"
            }`}
          >
            ${balance.toFixed(2)}
          </div>
        </div>
      </div>

      {/* Modal для создания новой заметки */}
      {showNewNoteForm && (
        <div
          style={{
            position: "fixed",
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            backgroundColor: "rgba(0, 0, 0, 0.8)",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            zIndex: 1000,
          }}
          onClick={() => setShowNewNoteForm(false)}
        >
          <div
            className="p-6 rounded-2xl border border-white/10 w-96 max-w-md"
            style={{
              background:
                "linear-gradient(135deg, rgba(255, 255, 255, 0.08), rgba(255, 255, 255, 0.02))",
              backdropFilter: "blur(20px)",
            }}
            onClick={(e) => e.stopPropagation()}
          >
            <h3 className="text-lg font-medium text-white mb-4">
              Add New Note
            </h3>
            <textarea
              value={newNoteTitle}
              onChange={(e) => {
                setNewNoteTitle(e.target.value);
                // Автоматически изменяем высоту
                e.target.style.height = "auto";
                e.target.style.height = e.target.scrollHeight + "px";
              }}
              placeholder="Enter note title..."
              className="w-full p-3 bg-white/10 border border-white/20 rounded-lg text-white placeholder-gray-400 mb-4 resize-none"
              onKeyPress={(e) => e.key === "Enter" && addNote()}
              autoFocus
              rows="1"
              style={{
                minHeight: "48px",
                height: "auto",
              }}
            />
            <div className="flex gap-2 justify-end">
              <button
                onClick={() => setShowNewNoteForm(false)}
                className="px-4 py-2 bg-white/10 hover:bg-white/20 rounded-lg text-white transition-colors"
              >
                Cancel
              </button>
              <button
                onClick={addNote}
                className="px-4 py-2 bg-white/20 hover:bg-white/30 rounded-lg text-white transition-colors"
              >
                Add
              </button>
            </div>
          </div>
        </div>
      )}

      {/* Main Notes Interface */}
      <div className="flex gap-6 min-h-96 items-start">
        {/* Левая панель со списком заметок */}
        <div
          className="w-80 p-6 rounded-2xl border border-white/10 h-fit flex-shrink-0"
          style={{
            background:
              "linear-gradient(135deg, rgba(255, 255, 255, 0.08), rgba(255, 255, 255, 0.02))",
            backdropFilter: "blur(20px)",
          }}
        >
          <div className="flex justify-between items-center mb-4">
            <h3 className="text-lg font-medium text-white">Notes</h3>
            <button
              onClick={() => setShowNewNoteForm(true)}
              className="p-2 bg-white/20 hover:bg-white/30 rounded-lg transition-colors"
            >
              <Plus size={16} className="text-white" />
            </button>
          </div>

          <div className="space-y-2">
            {notes.map((note) => (
              <div
                key={note.id}
                className={`p-3 rounded-lg cursor-pointer transition-colors flex justify-between items-start min-w-0 ${
                  activeNoteId === note.id
                    ? "bg-white/20 border border-white/30"
                    : "bg-white/5 hover:bg-white/10"
                }`}
                onClick={() => setActiveNoteId(note.id)}
              >
                <div className="flex items-center gap-2 min-w-0 flex-1">
                  <FileText size={16} className="text-white/60 flex-shrink-0" />
                  <span className="text-white text-sm break-words overflow-hidden">
                    {note.title}
                  </span>
                </div>
                {notes.length > 1 && (
                  <button
                    onClick={(e) => {
                      e.stopPropagation();
                      deleteNote(note.id);
                    }}
                    className="p-1 text-red-400 hover:text-red-300 transition-colors"
                  >
                    <X size={12} />
                  </button>
                )}
              </div>
            ))}
          </div>
        </div>

        {/* Правая панель с редактором */}
        <div
          className="flex-1 p-6 rounded-2xl border border-white/10 h-fit"
          style={{
            background:
              "linear-gradient(135deg, rgba(255, 255, 255, 0.08), rgba(255, 255, 255, 0.02))",
            backdropFilter: "blur(20px)",
          }}
        >
          <div className="flex justify-between items-center mb-4">
            {editingNoteId === activeNote?.id ? (
              <div className="flex gap-2 items-center flex-1">
                <input
                  type="text"
                  value={editingTitle}
                  onChange={(e) => setEditingTitle(e.target.value)}
                  onKeyPress={(e) => {
                    if (e.key === "Enter") saveEditedTitle();
                    if (e.key === "Escape") cancelEditing();
                  }}
                  className="flex-1 px-3 py-1 bg-white/10 border border-white/20 rounded text-white focus:outline-none focus:ring-2 focus:ring-white/20"
                  autoFocus
                />
                <button
                  onClick={saveEditedTitle}
                  className="px-3 py-1 bg-green-500/20 hover:bg-green-500/30 rounded text-green-400 transition-colors text-sm"
                >
                  Save
                </button>
                <button
                  onClick={cancelEditing}
                  className="px-3 py-1 bg-red-500/20 hover:bg-red-500/30 rounded text-red-400 transition-colors text-sm"
                >
                  Cancel
                </button>
              </div>
            ) : (
              <div className="flex justify-between items-center w-full">
                <h3
                  className="text-lg font-medium text-white break-words overflow-hidden cursor-pointer hover:text-white/80 transition-colors"
                  style={{ maxWidth: "70%", wordBreak: "break-word" }}
                  onClick={() => startEditingTitle(activeNote)}
                  title="Click to edit title"
                >
                  {activeNote?.title}
                </h3>
                {notes.length > 1 && (
                  <button
                    onClick={() => deleteNote(activeNote.id)}
                    className="px-3 py-1 bg-red-500/20 hover:bg-red-500/30 rounded text-red-400 transition-colors text-sm"
                  >
                    Delete Note
                  </button>
                )}
              </div>
            )}
          </div>

          {/* Поле для ввода */}
          <textarea
            ref={(textarea) => {
              if (textarea && activeNote?.content) {
                textarea.style.height = "auto";
                textarea.style.height = textarea.scrollHeight + "px";
              }
            }}
            className="w-full p-4 bg-white/5 rounded-lg border border-white/10 text-white focus:outline-none focus:ring-2 focus:ring-white/20 resize-none min-h-32"
            value={activeNote?.content || ""}
            onChange={(e) => {
              updateNoteContent(e.target.value);
              e.target.style.height = "auto";
              e.target.style.height = e.target.scrollHeight + "px";
            }}
            placeholder="Start writing your note..."
            style={{
              height: "auto",
              minHeight: "128px",
            }}
          />
        </div>
      </div>
    </div>
  );
};

export default Notes;
