import React from "react";
import { User, Star } from "lucide-react";

// Функции для расчета рекордов
const getBestTrade = (trades) => {
  const tradeTrades = trades.filter((trade) => trade.type === "trade");
  if (tradeTrades.length === 0) return "0.00";

  const bestTrade = Math.max(
    ...tradeTrades.map((trade) => trade.result - trade.commission)
  );
  return bestTrade.toFixed(2);
};

const getBestDay = (trades) => {
  const dailyProfits = {};

  trades.forEach((trade) => {
    if (trade.type === "trade") {
      // Добавляем фильтр только для сделок
      if (!dailyProfits[trade.date]) {
        dailyProfits[trade.date] = 0;
      }
      dailyProfits[trade.date] += trade.result - trade.commission;
    }
  });

  const bestDay =
    Object.keys(dailyProfits).length > 0
      ? Math.max(...Object.values(dailyProfits))
      : 0;
  return bestDay.toFixed(2);
};

const getBestWeek = (trades) => {
  const weeklyProfits = {};

  trades.forEach((trade) => {
    if (trade.type === "trade") {
      const date = new Date(trade.date);
      const week = getWeekKey(date);

      if (!weeklyProfits[week]) {
        weeklyProfits[week] = 0;
      }

      weeklyProfits[week] += trade.result - trade.commission;
    }
  });

  const bestWeek =
    Object.keys(weeklyProfits).length > 0
      ? Math.max(...Object.values(weeklyProfits))
      : 0;
  return bestWeek.toFixed(2);
};

const getBestMonth = (trades) => {
  const monthlyProfits = {};

  trades.forEach((trade) => {
    if (trade.type === "trade") {
      const date = new Date(trade.date);
      const month = `${date.getFullYear()}-${String(
        date.getMonth() + 1
      ).padStart(2, "0")}`;

      if (!monthlyProfits[month]) {
        monthlyProfits[month] = 0;
      }

      monthlyProfits[month] += trade.result - trade.commission;
    }
  });

  const bestMonth =
    Object.keys(monthlyProfits).length > 0
      ? Math.max(...Object.values(monthlyProfits))
      : 0;
  return bestMonth.toFixed(2);
};

const getBestYear = (trades) => {
  const yearlyProfits = {};

  trades.forEach((trade) => {
    if (trade.type === "trade") {
      const year = new Date(trade.date).getFullYear();

      if (!yearlyProfits[year]) {
        yearlyProfits[year] = 0;
      }

      yearlyProfits[year] += trade.result - trade.commission;
    }
  });

  const bestYear =
    Object.keys(yearlyProfits).length > 0
      ? Math.max(...Object.values(yearlyProfits))
      : 0;
  return bestYear.toFixed(2);
};

// Вспомогательная функция для определения недели
const getWeekKey = (date) => {
  // Получаем понедельник текущей недели
  const dayOfWeek = date.getDay(); // 0 = воскресенье, 1 = понедельник, и т.д.
  const mondayOffset = dayOfWeek === 0 ? -6 : 1 - dayOfWeek; // Если воскресенье, то -6, иначе 1-dayOfWeek

  const monday = new Date(date);
  monday.setDate(date.getDate() + mondayOffset);

  // Форматируем дату понедельника как ключ
  const year = monday.getFullYear();
  const month = String(monday.getMonth() + 1).padStart(2, "0");
  const day = String(monday.getDate()).padStart(2, "0");

  return `${year}-${month}-${day}`;
};

// Компонент карточки профиля
const ProfileCard = ({ rank, balance, userProfile }) => {
  // Функция для получения названия следующего ранга по балансу
  const getNextRankName = (nextBalance) => {
    if (nextBalance >= 10000000) return "SSS";
    if (nextBalance >= 1000000) return "SSS";
    if (nextBalance >= 100000) return "S+";
    if (nextBalance >= 50000) return "S";
    if (nextBalance >= 10000) return "A";
    if (nextBalance >= 5000) return "B";
    if (nextBalance >= 1000) return "C";
    if (nextBalance >= 500) return "D";
    if (nextBalance >= 100) return "E";
    if (nextBalance >= 10) return "F";
    return "Unranked";
  };

  const profileData = {
    avatar: userProfile.avatar,
    nickname: userProfile.nickname,
    currentRank: rank.name,
    rankProgress: rank.nextRank
      ? Math.round(
          ((balance - rank.minBalance) / (rank.nextRank - rank.minBalance)) *
            100
        )
      : 100,
    nextRank: rank.nextRank ? getNextRankName(rank.nextRank) : "Max Level",
  };

  return (
    <div
      className="p-6 rounded-2xl border border-white/10"
      style={{
        background:
          "linear-gradient(135deg, rgba(255, 255, 255, 0.08), rgba(255, 255, 255, 0.02))",
        backdropFilter: "blur(20px)",
      }}
    >
      {/* Основное содержимое */}
      <div className="flex justify-between">
        {/* Левая часть - весь текст */}
        <div className="flex-1">
          <h2 className="text-xl font-medium text-white mb-4">Profile</h2>

          <h3 className="text-lg font-semibold text-white mb-1">
            {profileData.nickname}
          </h3>
          <div className="flex items-center space-x-1 mb-6">
            <Star className="w-4 h-4 text-yellow-400" />
            <span className="text-sm text-white/60">
              Rank {profileData.currentRank}
            </span>
          </div>
        </div>

        {/* Правая часть - аватарка */}
        <div className="ml-6">
          <div className="w-24 h-24 rounded-full bg-white/10 flex items-center justify-center text-5xl overflow-hidden">
            {typeof profileData.avatar === "string" &&
            profileData.avatar.startsWith("data:") ? (
              <img
                src={profileData.avatar}
                alt="Avatar"
                className="w-full h-full object-cover"
                style={{ width: "100%", height: "100%", objectFit: "cover" }}
              />
            ) : (
              <span className="text-5xl">{profileData.avatar}</span>
            )}
          </div>
        </div>
      </div>

      {/* Прогресс до следующего ранга */}
      <div>
        <div className="flex justify-between items-center mb-2">
          <span className="text-sm text-white/60">
            Progress to {profileData.nextRank}
          </span>
          <span className="text-sm text-white/60">
            {profileData.rankProgress}%
          </span>
        </div>
        <div className="w-full bg-white/10 rounded-full h-2">
          <div
            className="bg-green-400 h-2 rounded-full transition-all duration-300"
            style={{ width: `${profileData.rankProgress}%` }}
          ></div>
        </div>
      </div>
    </div>
  );
};

// Компонент календаря встроен в Dashboard
const CalendarCard = ({ trades }) => {
  const generateCalendarDays = () => {
    const today = new Date();
    const currentDate = today.getDate();

    // Находим начало текущей недели (понедельник)
    const dayOfWeek = today.getDay();
    const mondayOffset = dayOfWeek === 0 ? -6 : 1 - dayOfWeek; // Если воскресенье, то -6, иначе 1-dayOfWeek
    const monday = new Date(today);
    monday.setDate(today.getDate() + mondayOffset);

    const days = [];

    // Создаем 7 дней начиная с понедельника
    for (let i = 0; i < 7; i++) {
      const date = new Date(monday);
      date.setDate(monday.getDate() + i);

      const dateString = date.toDateString();

      // Находим все трейды для этого дня
      const dayTrades = trades.filter((trade) => {
        const tradeDate = new Date(trade.date + "T00:00:00"); // Принудительно локальное время
        return tradeDate.toDateString() === dateString;
      });

      // Считаем прибыль за день
      const tradingTrades = dayTrades.filter((trade) => trade.type === "trade");
      const dayProfit = tradingTrades.reduce((sum, trade) => {
        return sum + trade.result - trade.commission;
      }, 0);

      days.push({
        date: date.getDate(),
        day: date.toLocaleDateString("en", { weekday: "short" }),
        profit:
          dayProfit === 0
            ? "$0"
            : dayProfit > 0
            ? `+$${dayProfit.toFixed(0)}`
            : `-$${Math.abs(dayProfit).toFixed(0)}`,
        trades: dayTrades.filter((trade) => trade.type === "trade").length,
        isToday: date.toDateString() === today.toDateString(),
      });
    }

    return days;
  };

  const calendarDays = generateCalendarDays();

  return (
    <div
      className="p-6 rounded-2xl border border-white/10"
      style={{
        background:
          "linear-gradient(135deg, rgba(255, 255, 255, 0.08), rgba(255, 255, 255, 0.02))",
        backdropFilter: "blur(20px)",
      }}
    >
      {/* Заголовок с месяцем */}
      <div className="mb-6">
        <h2 className="text-xl font-medium text-white">
          {`${new Date().toLocaleDateString("en", {
            month: "long",
          })}, ${new Date().getFullYear()}`}
        </h2>
      </div>

      {/* Дни календаря */}
      <div className="grid grid-cols-7 gap-4">
        {calendarDays.map((day) => (
          <div
            key={day.date}
            className={`p-4 rounded-lg ${
              day.isToday ? "bg-white/10 border border-white/20" : "bg-white/5"
            }`}
          >
            <div className="flex items-center gap-2 mb-2">
              <div className="text-xl font-medium text-white">{day.date}</div>
              <div className="text-sm text-gray-400">{day.day}</div>
            </div>
            <div
              className={`text-sm font-medium ${
                day.profit.includes("+")
                  ? "text-emerald-400"
                  : day.profit.includes("-")
                  ? "text-red-400"
                  : "text-gray-400"
              }`}
            >
              {day.profit}
            </div>
            <div className="text-sm text-gray-400">{day.trades} trades</div>
          </div>
        ))}
      </div>
    </div>
  );
};

const Dashboard = ({
  balance,
  trades,
  rank,
  achievements = [],
  tasks = [],
  userProfile,
}) => {
  return (
    <div className="space-y-6">
      <div className="flex justify-between items-center mb-8">
        <h1 className="text-4xl font-light text-white">Dashboard</h1>
        <div className="text-right">
          <div className="text-sm text-white/60">Balance</div>
          <div
            className={`text-2xl font-semibold ${
              balance >= 0 ? "text-green-400" : "text-red-400"
            }`}
          >
            ${balance.toFixed(2)}
          </div>
        </div>
      </div>

      {/* Сетка с профилем и календарем */}
      <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
        {/* Календарь занимает 2 колонки */}
        <div className="lg:col-span-2">
          <CalendarCard trades={trades} />
        </div>

        {/* Профиль занимает 1 колонку */}
        <div>
          <ProfileCard
            rank={rank}
            balance={balance}
            userProfile={userProfile}
          />
        </div>
      </div>
      {/* Вторая строка карточек */}
      <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
        {/* Achievements */}
        <div
          className="p-6 rounded-2xl border border-white/10"
          style={{
            background:
              "linear-gradient(135deg, rgba(255, 255, 255, 0.08), rgba(255, 255, 255, 0.02))",
            backdropFilter: "blur(20px)",
          }}
        >
          <h2 className="text-xl font-medium text-white mb-4">Achievements</h2>

          {achievements.length === 0 ? (
            <p className="text-white/60 text-sm">No achievements yet</p>
          ) : (
            <div className="space-y-3">
              {Object.entries({
                levelup: { name: "Level UP", icon: "🚀" },
                trade_profit: { name: "Trade Profit", icon: "💵" },
                daily_profit: { name: "Daily Profit", icon: "💰" },
                stability: { name: "Stability", icon: "📈" },
                discipline: { name: "Discipline", icon: "💎" },
              }).map(([categoryKey, category]) => {
                const categoryAchievements = achievements.filter(
                  (a) => a.category === categoryKey
                );
                const completed = categoryAchievements.filter(
                  (a) => a.completed
                ).length;
                // Находим текущую незавершенную ачивку
                const currentAchievement = categoryAchievements.find(
                  (a) => !a.completed
                );

                // Рассчитываем прогресс с учетом категории
                let progress = 0;
                if (currentAchievement) {
                  if (currentAchievement.category === "levelup") {
                    const rankOrder = [
                      "F",
                      "E",
                      "D",
                      "C",
                      "B",
                      "A",
                      "S",
                      "S+",
                      "SSS",
                    ];
                    const currentRankIndex = rankOrder.indexOf(rank.name);
                    const targetRankIndex = rankOrder.indexOf(
                      currentAchievement.value
                    );

                    if (currentRankIndex >= targetRankIndex) {
                      progress = 100;
                    } else {
                      progress = rank.nextRank
                        ? Math.round(
                            ((balance - rank.minBalance) /
                              (rank.nextRank - rank.minBalance)) *
                              100
                          )
                        : 100;
                    }
                  } else {
                    progress = currentAchievement.progress || 0;
                  }
                } else if (completed === categoryAchievements.length) {
                  progress = 100;
                }

                if (categoryAchievements.length === 0) return null;

                return (
                  <div key={categoryKey} className="space-y-2">
                    <div className="flex items-center justify-between">
                      <div className="flex items-center gap-2">
                        <span className="text-lg">{category.icon}</span>
                        <span className="text-sm text-white">
                          {category.name}
                        </span>
                      </div>
                      <span className="text-xs text-white/60">
                        {completed}/{categoryAchievements.length}
                      </span>
                    </div>
                    <div className="w-full bg-white/10 rounded-full h-2">
                      <div
                        className="bg-green-400 h-2 rounded-full transition-all duration-300"
                        style={{ width: `${progress}%` }}
                      ></div>
                    </div>
                  </div>
                );
              })}
            </div>
          )}
        </div>
        {/* Daily Tasks */}
        <div
          className="p-6 rounded-2xl border border-white/10"
          style={{
            background:
              "linear-gradient(135deg, rgba(255, 255, 255, 0.08), rgba(255, 255, 255, 0.02))",
            backdropFilter: "blur(20px)",
          }}
        >
          <h2 className="text-xl font-medium text-white mb-4">Daily Tasks</h2>

          {tasks.length === 0 ? (
            <p className="text-white/50 text-sm">No tasks for today yet</p>
          ) : (
            <div className="space-y-2">
              {tasks.slice(0, 3).map(
                (
                  task // Показываем только первые 3 задачи
                ) => (
                  <div key={task.id} className="flex items-start gap-2">
                    <div
                      className={`w-3 h-3 rounded-full ${
                        task.completed ? "bg-green-400" : "bg-white/30"
                      }`}
                    ></div>
                    <span
                      className={`text-sm leading-relaxed ${
                        task.completed
                          ? "text-white/60 line-through"
                          : "text-white"
                      }`}
                      style={{
                        wordBreak: "break-word",
                        whiteSpace: "normal",
                        display: "block",
                      }}
                    >
                      {task.text}
                    </span>
                  </div>
                )
              )}
              {tasks.length > 3 && (
                <p className="text-xs text-white/50 mt-2">
                  +{tasks.length - 3} more tasks
                </p>
              )}
            </div>
          )}
        </div>

        <div
          className="p-6 rounded-2xl border border-white/10"
          style={{
            background:
              "linear-gradient(135deg, rgba(255, 255, 255, 0.08), rgba(255, 255, 255, 0.02))",
            backdropFilter: "blur(20px)",
          }}
        >
          <h2 className="text-xl font-medium text-white mb-4">Records</h2>

          <div className="space-y-3">
            <div className="flex justify-between items-center">
              <span className="text-sm text-white/60">Best Trade</span>
              <span className="text-sm font-semibold text-green-400">
                ${getBestTrade(trades)}
              </span>
            </div>
            <div className="flex justify-between items-center">
              <span className="text-sm text-white/60">Best Day</span>
              <span className="text-sm font-semibold text-green-400">
                ${getBestDay(trades)}
              </span>
            </div>
            <div className="flex justify-between items-center">
              <span className="text-sm text-white/60">Best Week</span>
              <span className="text-sm font-semibold text-green-400">
                ${getBestWeek(trades)}
              </span>
            </div>
            <div className="flex justify-between items-center">
              <span className="text-sm text-white/60">Best Month</span>
              <span className="text-sm font-semibold text-green-400">
                ${getBestMonth(trades)}
              </span>
            </div>
            <div className="flex justify-between items-center">
              <span className="text-sm text-white/60">Best Year</span>
              <span className="text-sm font-semibold text-green-400">
                ${getBestYear(trades)}
              </span>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Dashboard;
