import React, { useState, useEffect } from "react";
import { useDataStore } from "./hooks/useDataStore";
import Sidebar from "./components/Sidebar";
import Dashboard from "./components/Dashboard";
import TradingJournal from "./components/Trades";
import Reports from "./components/Reports";
import Achievements from "./components/Achievements";
import Tasks from "./components/Tasks";
import Notes from "./components/Notes";
import Settings from "./components/Settings";
import { useAuth } from "./hooks/useAuth";
import { useFirestore } from "./hooks/useFirestore";
import AuthComponent from "./components/AuthComponent";

const App = () => {
  const [activeSection, setActiveSection] = useState("dashboard");
  const [isSidebarCollapsed, setIsSidebarCollapsed] = useState(false);

  // Основные данные
  const [trades, setTrades] = useState([]);
  const [achievements, setAchievements] = useState([]);
  const [dailyResults, setDailyResults] = useState([]);
  const [tasks, setTasks] = useState([]);
  const [taskHistory, setTaskHistory] = useState({});
  const [userProfile, setUserProfile] = useState({
    avatar: "👨‍💼",
    nickname: "TradeMaster",
  });
  const [notes, setNotes] = useState([
    { id: 1, title: "First Note", content: "", type: "text" },
  ]);

  const { user, loading: authLoading } = useAuth();
  const { saveUserData, loadUserData, firebaseLoaded } = useFirestore();
  const { loadData, saveData, clearAllData } = useDataStore();

  // -------------------------
  // Загрузка данных при старте
  // -------------------------
  useEffect(() => {
    const loadInitialData = async () => {
      let initialData = null;

      if (user) {
        try {
          const firebaseData = await loadUserData();
          initialData = {
            trades: Array.isArray(firebaseData?.trades)
              ? firebaseData.trades
              : [],
            achievements: Array.isArray(firebaseData?.achievements)
              ? firebaseData.achievements
              : [],
            dailyResults: Array.isArray(firebaseData?.dailyResults)
              ? firebaseData.dailyResults
              : [],
            tasks: Array.isArray(firebaseData?.tasks) ? firebaseData.tasks : [],
            taskHistory: firebaseData?.taskHistory || {},
            userProfile: firebaseData?.userProfile || {
              avatar: "👨‍💼",
              nickname: "TradeMaster",
            },
            notes: Array.isArray(firebaseData?.notes)
              ? firebaseData.notes
              : [{ id: 1, title: "First Note", content: "", type: "text" }],
          };
        } catch (err) {
          console.error("Ошибка загрузки данных Firebase:", err);
        }
      }

      if (!initialData) {
        const localData = loadData("allData") || {};
        initialData = {
          trades: localData.trades || [],
          achievements: localData.achievements || [],
          dailyResults: localData.dailyResults || [],
          tasks: localData.tasks || [],
          taskHistory: localData.taskHistory || {},
          userProfile: localData.userProfile || {
            avatar: "👨‍💼",
            nickname: "TradeMaster",
          },
          notes: localData.notes || [
            { id: 1, title: "First Note", content: "", type: "text" },
          ],
        };
      }

      setTrades(initialData.trades);
      setAchievements(initialData.achievements);
      setDailyResults(initialData.dailyResults);
      setTasks(initialData.tasks);
      setTaskHistory(initialData.taskHistory);
      setUserProfile(initialData.userProfile);
      setNotes(initialData.notes);

      checkAndClearDailyTasks();
    };

    loadInitialData();
  }, [user]);

  // Проверка смены дня каждые 60 секунд
  useEffect(() => {
    const checkDayChange = () => {
      checkAndClearDailyTasks();
    };

    // Проверяем сразу при загрузке
    checkDayChange();

    // Устанавливаем интервал проверки каждые 60 секунд
    const interval = setInterval(checkDayChange, 60000);

    return () => clearInterval(interval);
  }, [tasks, taskHistory]); // Добавляем зависимости

  // -------------------------
  // Автосохранение всех данных (только после загрузки Firebase)
  // -------------------------
  useEffect(() => {
    if (!user) return;
    const allData = {
      trades,
      achievements,
      dailyResults,
      tasks,
      taskHistory,
      userProfile,
      notes,
    };

    saveData("allData", allData);

    if (firebaseLoaded) {
      saveUserData(allData).catch((err) =>
        console.error("Ошибка сохранения в Firebase:", err)
      );
    }
  }, [
    trades,
    achievements,
    dailyResults,
    tasks,
    taskHistory,
    userProfile,
    notes,
    user,
    firebaseLoaded,
  ]);

  // -------------------------
  // Функции дневного результата и очистки задач
  // -------------------------
  const saveDayResult = (dateToSave) => {
    if (!dateToSave) return;

    // Конвертируем dateToSave в формат YYYY-MM-DD для сравнения с trades
    const dateForComparison = new Date(dateToSave).toISOString().split("T")[0];

    const dayTrades = trades.filter(
      (t) => t.date === dateForComparison && t.type === "trade"
    );

    if (dayTrades.length === 0) return;

    const dayProfit = dayTrades.reduce(
      (sum, t) => sum + t.result - t.commission,
      0
    );
    const allSystematic = dayTrades.every((t) => t.isSystematic === "Да");

    const dayResult = {
      date: dateForComparison,
      profit: dayProfit,
      tradesCount: dayTrades.length,
      isProfit: dayProfit > 0,
      isSystematic: allSystematic,
    };

    setDailyResults((prev) => {
      const exists = prev.find((r) => r.date === dateForComparison);
      if (exists) return prev;
      return [...prev, dayResult];
    });
  };

  const checkAndClearDailyTasks = () => {
    const today = new Date().toDateString(); // Используем toDateString() вместо ISO
    const lastClearDate = localStorage.getItem("lastClearDate");

    if (lastClearDate && lastClearDate !== today) {
      // Сохраняем результат предыдущего дня
      saveDayResult(lastClearDate);

      // Переносим задачи в историю, если они есть
      if (tasks.length > 0) {
        setTaskHistory((prev) => ({
          ...prev,
          [lastClearDate]: [...tasks],
        }));
      }

      // Очищаем текущие задачи
      setTasks([]);
      console.log(
        `Задачи очищены. Данные за ${lastClearDate} сохранены в историю`
      );
    }

    localStorage.setItem("lastClearDate", today);
  };

  // -------------------------
  // Сброс всех данных
  // -------------------------
  const resetAllData = async () => {
    const defaultData = {
      trades: [],
      achievements: [],
      dailyResults: [],
      tasks: [],
      taskHistory: {},
      userProfile: { avatar: "👨‍💼", nickname: "TradeMaster" },
      notes: [{ id: 1, title: "First Note", content: "", type: "text" }],
    };

    setTrades(defaultData.trades);
    setAchievements(defaultData.achievements);
    setDailyResults(defaultData.dailyResults);
    setTasks(defaultData.tasks);
    setTaskHistory(defaultData.taskHistory);
    setUserProfile(defaultData.userProfile);
    setNotes(defaultData.notes);

    clearAllData();
    if (user && firebaseLoaded) {
      try {
        await saveUserData(defaultData);
      } catch (error) {
        console.error("Ошибка при сбросе данных в Firebase:", error);
      }
    }
    // Очищаем localStorage от даты последней очистки задач
    localStorage.removeItem("lastClearDate");
  };

  // -------------------------
  // Баланс и ранги
  // -------------------------
  const calculateBalance = () =>
    trades.reduce((total, trade) => {
      if (trade.type === "deposit_operation") {
        return trade.operationType === "deposit"
          ? total + trade.amount
          : total - trade.amount;
      } else {
        return total + trade.result - trade.commission;
      }
    }, 0);

  const balance = calculateBalance();

  const calculateRank = (balance) => {
    if (balance >= 10000000)
      return {
        name: "SSS",
        color: "#ff1493",
        nextRank: null,
        minBalance: 10000000,
      };
    if (balance >= 1000000)
      return {
        name: "SSS",
        color: "#ff1493",
        nextRank: 10000000,
        minBalance: 1000000,
      };
    if (balance >= 100000)
      return {
        name: "S+",
        color: "#ffd700",
        nextRank: 1000000,
        minBalance: 100000,
      };
    if (balance >= 50000)
      return {
        name: "S",
        color: "#ffa500",
        nextRank: 100000,
        minBalance: 50000,
      };
    if (balance >= 10000)
      return {
        name: "A",
        color: "#00ff88",
        nextRank: 50000,
        minBalance: 10000,
      };
    if (balance >= 5000)
      return { name: "B", color: "#40e0d0", nextRank: 10000, minBalance: 5000 };
    if (balance >= 1000)
      return { name: "C", color: "#9370db", nextRank: 5000, minBalance: 1000 };
    if (balance >= 500)
      return { name: "D", color: "#87ceeb", nextRank: 1000, minBalance: 500 };
    if (balance >= 100)
      return { name: "E", color: "#daa520", nextRank: 500, minBalance: 100 };
    if (balance >= 10)
      return { name: "F", color: "#ff6b6b", nextRank: 100, minBalance: 10 };
    return { name: "Unranked", color: "#808080", nextRank: 10, minBalance: 0 };
  };

  const rank = calculateRank(balance);

  // -------------------------
  // Контент по секциям
  // -------------------------
  const renderContent = () => {
    switch (activeSection) {
      case "dashboard":
        return (
          <Dashboard
            balance={balance}
            trades={trades}
            rank={rank}
            achievements={achievements}
            tasks={tasks}
            userProfile={userProfile}
          />
        );
      case "trades":
        return (
          <TradingJournal
            trades={trades}
            setTrades={setTrades}
            balance={balance}
            onTradesChange={setTrades}
          />
        );
      case "reports":
        return (
          <Reports
            balance={balance}
            trades={trades}
            achievements={achievements}
            tasks={tasks}
            taskHistory={taskHistory}
          />
        );
      case "achievements":
        return (
          <Achievements
            balance={balance}
            rank={rank}
            trades={trades}
            achievements={achievements}
            setAchievements={setAchievements}
            dailyResults={dailyResults}
          />
        );
      case "tasks":
        return (
          <Tasks
            balance={balance}
            tasks={tasks}
            setTasks={setTasks}
            taskHistory={taskHistory}
          />
        );
      case "notes":
        return <Notes balance={balance} notes={notes} setNotes={setNotes} />;
      case "settings":
        return (
          <Settings
            balance={balance}
            userProfile={userProfile}
            setUserProfile={setUserProfile}
            resetAllData={resetAllData}
          />
        );
      default:
        return <Dashboard balance={balance} />;
    }
  };

  // -------------------------
  // Рендеринг
  // -------------------------
  if (authLoading) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gradient-to-br from-gray-900 to-black text-white">
        <div className="text-xl">Loading...</div>
      </div>
    );
  }

  if (!user) {
    return <AuthComponent />;
  }

  return (
    <div
      className="min-h-screen text-white"
      style={{
        fontFamily:
          'Exo, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, sans-serif',
        background: "linear-gradient(135deg, #0a0a0a, #1a1a1a, #2d2d2d)",
        overflowX: "hidden",
      }}
    >
      <div className="flex min-h-screen">
        <Sidebar
          activeSection={activeSection}
          setActiveSection={setActiveSection}
          isCollapsed={isSidebarCollapsed}
          setIsCollapsed={setIsSidebarCollapsed}
        />
        <div className="flex-1">
          <div className="p-8">{renderContent()}</div>
        </div>
      </div>
    </div>
  );
};

export default App;
