import React, { useState, useEffect } from "react";
import { Plus, Trash2 } from "lucide-react";

const Achievements = ({
  balance,
  rank,
  trades = [],
  achievements,
  setAchievements,
  dailyResults = [],
}) => {
  const [showForm, setShowForm] = useState(false);
  const [selectedCategory, setSelectedCategory] = useState(null);
  const [formData, setFormData] = useState({
    name: "",
    rank: "F",
    amount: "",
    timeCount: "",
    timeUnit: "days",
    event: "",
  });
  // Добавь этот useEffect после объявления всех useState
  React.useEffect(() => {
    let hasChanges = false;

    const updatedAchievements = achievements.map((achievement) => {
      // Создаем копию достижения
      const updatedAchievement = { ...achievement };
      const wasCompleted = achievement.completed;

      if (!achievement.completed) {
        const newCurrent = updateAchievementProgress(achievement);
        const newCompleted = checkAchievementCompletion(achievement);

        updatedAchievement.current = newCurrent;
        updatedAchievement.completed = newCompleted;

        if (newCompleted && !wasCompleted) {
          updatedAchievement.dateCompleted = new Date()
            .toISOString()
            .split("T")[0];
          hasChanges = true;
        }
      }

      // Если достижение уже завершено, но у него нет даты
      if (achievement.completed && !achievement.dateCompleted) {
        const yesterday = new Date();
        yesterday.setDate(yesterday.getDate() - 1);
        updatedAchievement.dateCompleted = yesterday
          .toISOString()
          .split("T")[0];
        hasChanges = true;
      }

      return updatedAchievement;
    });

    // Обновляем только если есть реальные изменения
    if (hasChanges) {
      setAchievements(updatedAchievements);
    }
  }, [trades.length, balance, rank.name]); // Используем примитивные значения вместо объектов

  // Функции для работы с данными сделок
  const getTodayTrades = () => {
    const today = new Date().toISOString().split("T")[0];
    return trades.filter(
      (trade) => trade.date === today && trade.type === "trade"
    );
  };

  const getDailyProfit = () => {
    const todayTrades = getTodayTrades();
    return todayTrades.reduce(
      (total, trade) => total + trade.result - trade.commission,
      0
    );
  };

  const getMaxSingleTradeProfit = () => {
    const tradeProfits = trades
      .filter((trade) => trade.type === "trade")
      .map((trade) => trade.result - trade.commission);
    return tradeProfits.length > 0 ? Math.max(...tradeProfits) : 0;
  };

  // Новые функции для расчета Stability и Discipline
  const getDateRangeStart = (date, unit) => {
    const d = new Date(date);

    switch (unit) {
      case "days":
        return d.toISOString().split("T")[0];
      case "weeks":
        // Получаем понедельник текущей недели
        const dayOfWeek = d.getDay();
        const diff = d.getDate() - dayOfWeek + (dayOfWeek === 0 ? -6 : 1);
        return new Date(d.setDate(diff)).toISOString().split("T")[0];
      case "months":
        // Первое число месяца
        return new Date(d.getFullYear(), d.getMonth(), 1)
          .toISOString()
          .split("T")[0];
      case "years":
        // Первое января текущего года
        return new Date(d.getFullYear(), 0, 1).toISOString().split("T")[0];
      default:
        return d.toISOString().split("T")[0];
    }
  };

  const getDateRangeEnd = (date, unit) => {
    const d = new Date(date);

    switch (unit) {
      case "days":
        return d.toISOString().split("T")[0];
      case "weeks":
        // Получаем воскресенье текущей недели
        const dayOfWeek = d.getDay();
        const diff = d.getDate() - dayOfWeek + (dayOfWeek === 0 ? 0 : 7);
        return new Date(d.setDate(diff)).toISOString().split("T")[0];
      case "months":
        // Последний день месяца
        return new Date(d.getFullYear(), d.getMonth() + 1, 0)
          .toISOString()
          .split("T")[0];
      case "years":
        // 31 декабря текущего года
        return new Date(d.getFullYear(), 11, 31).toISOString().split("T")[0];
      default:
        return d.toISOString().split("T")[0];
    }
  };

  const getPeriodResult = (startDate, endDate, type) => {
    const periodTrades = trades.filter((trade) => {
      if (trade.type !== "trade") return false;
      return trade.date >= startDate && trade.date <= endDate;
    });

    if (periodTrades.length === 0) return null;

    if (type === "stability") {
      // Для стабильности нужен положительный результат
      const totalProfit = periodTrades.reduce(
        (sum, trade) => sum + trade.result - trade.commission,
        0
      );
      return totalProfit > 0;
    } else if (type === "discipline") {
      // Для дисциплины все сделки должны быть системными
      return periodTrades.every((trade) => trade.isSystematic === "Да");
    }

    return false;
  };

  const getPreviousPeriod = (date, unit) => {
    const d = new Date(date);

    switch (unit) {
      case "days":
        return new Date(d.setDate(d.getDate() - 1));
      case "weeks":
        return new Date(d.setDate(d.getDate() - 7));
      case "months":
        return new Date(d.setMonth(d.getMonth() - 1));
      case "years":
        return new Date(d.setFullYear(d.getFullYear() - 1));
      default:
        return new Date(d.setDate(d.getDate() - 1));
    }
  };

  const calculateStreaks = (type, timeUnit, targetCount) => {
    if (trades.length === 0) return 0;

    // Определяем текущую дату и проверяем, завершился ли текущий период
    const now = new Date();
    let currentDate = new Date(now);

    // Для проверки завершения периода нужно знать, находимся ли мы в его конце
    const isCurrentPeriodComplete = (date, unit) => {
      const today = new Date();

      switch (unit) {
        case "days":
          // День завершается в полночь, но мы можем проверять в течение дня
          return true;
        case "weeks":
          // Неделя завершается в воскресенье, проверяем только с понедельника
          return today.getDay() !== 0; // Не воскресенье = период еще не завершен полностью
        case "months":
          // Месяц завершается в последний день, проверяем только с 1 числа следующего месяца
          const lastDayOfMonth = new Date(
            today.getFullYear(),
            today.getMonth() + 1,
            0
          ).getDate();
          return today.getDate() !== lastDayOfMonth; // Не последний день = период не завершен
        case "years":
          // Год завершается 31 декабря, проверяем только с 1 января следующего года
          return !(today.getMonth() === 11 && today.getDate() === 31); // Не 31 декабря = период не завершен
        default:
          return true;
      }
    };

    let streak = 0;

    // Начинаем с предыдущего завершенного периода
    switch (timeUnit) {
      case "days":
        // For days, start from yesterday
        currentDate.setDate(currentDate.getDate() - 1);
        break;
      case "weeks":
        // For weeks, start from previous week
        const daysToLastSunday =
          currentDate.getDay() === 0 ? 7 : currentDate.getDay();
        currentDate.setDate(currentDate.getDate() - daysToLastSunday);
        break;
      case "months":
        // For months, start from previous month
        currentDate.setMonth(currentDate.getMonth() - 1);
        break;
      case "years":
        // For years, start from previous year
        currentDate.setFullYear(currentDate.getFullYear() - 1);
        break;
    }

    for (let i = 0; i < targetCount + 10; i++) {
      const startDate = getDateRangeStart(currentDate, timeUnit);
      const endDate = getDateRangeEnd(currentDate, timeUnit);

      const periodResult = getPeriodResult(startDate, endDate, type);

      if (periodResult === null) {
        // Нет сделок в этом периоде, переходим к предыдущему
        currentDate = getPreviousPeriod(currentDate, timeUnit);
        continue;
      }

      if (periodResult) {
        streak++;
      } else {
        break; // Прерываем серию
      }

      if (streak >= targetCount) break;

      currentDate = getPreviousPeriod(currentDate, timeUnit);
    }

    return streak;
  };

  const getStabilityStreak = (timeUnit, targetCount) => {
    return calculateStreaks("stability", timeUnit, targetCount);
  };

  const getDisciplineStreak = (timeUnit, targetCount) => {
    return calculateStreaks("discipline", timeUnit, targetCount);
  };

  const checkAchievementCompletion = (achievement) => {
    switch (achievement.category) {
      case "levelup":
        const rankOrder = ["F", "E", "D", "C", "B", "A", "S", "S+", "SSS"];
        const currentRankIndex = rankOrder.indexOf(rank.name);
        const targetRankIndex = rankOrder.indexOf(achievement.value);
        return currentRankIndex >= targetRankIndex;
      case "trade_profit":
        return getMaxSingleTradeProfit() >= achievement.value;
      case "daily_profit":
        return getDailyProfit() >= achievement.value;
      case "stability":
        return (
          getStabilityStreak(achievement.timeUnit, achievement.value) >=
          achievement.value
        );
      case "discipline":
        return (
          getDisciplineStreak(achievement.timeUnit, achievement.value) >=
          achievement.value
        );
      default:
        return false;
    }
  };

  const updateAchievementProgress = (achievement) => {
    switch (achievement.category) {
      case "levelup":
        return rank.name === achievement.value ? achievement.value : rank.name;
      case "trade_profit":
        return Math.min(getMaxSingleTradeProfit(), achievement.value);
      case "daily_profit":
        return Math.min(getDailyProfit(), achievement.value);
      case "stability":
        return Math.min(
          getStabilityStreak(achievement.timeUnit, achievement.value),
          achievement.value
        );
      case "discipline":
        return Math.min(
          getDisciplineStreak(achievement.timeUnit, achievement.value),
          achievement.value
        );
      default:
        return 0;
    }
  };

  const achievementCategories = {
    levelup: {
      name: "Level UP",
      icon: "🚀",
      description: "Achieve specific trading ranks",
    },
    trade_profit: {
      name: "Trade Profit",
      icon: "💵",
      description: "High profit from single trade",
    },
    daily_profit: {
      name: "Daily Profit",
      icon: "💰",
      description: "Daily profit achievements",
    },
    stability: {
      name: "Stability",
      icon: "📈",
      description: "Consistent profitable trading streaks",
    },
    discipline: {
      name: "Discipline",
      icon: "💎",
      description: "Systematic trading streaks",
    },
  };

  const rankOptions = ["F", "E", "D", "C", "B", "A", "S", "S+", "SSS"];
  const timeUnits = ["days", "weeks", "months", "years"];

  const styles = {
    card: {
      background:
        "linear-gradient(135deg, rgba(255, 255, 255, 0.08), rgba(255, 255, 255, 0.02))",
      backdropFilter: "blur(20px)",
      borderRadius: "16px",
      border: "1px solid rgba(255, 255, 255, 0.1)",
      padding: "24px",
      maxWidth: "none",
    },
    input: {
      width: "100%",
      padding: "12px",
      background: "rgba(255, 255, 255, 0.1)",
      border: "1px solid rgba(255, 255, 255, 0.2)",
      borderRadius: "8px",
      color: "#ffffff",
      fontSize: "14px",
    },
  };

  const openForm = (categoryId) => {
    setSelectedCategory(categoryId);
    setFormData({
      name: "",
      rank: "F",
      amount: "",
      timeCount: "",
      timeUnit: "days",
      event: "",
    });
    setShowForm(true);
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    let newAchievement = {
      id: Date.now(),
      category: selectedCategory,
      completed: false,
      current: 0,
    };

    switch (selectedCategory) {
      case "levelup":
        newAchievement = {
          ...newAchievement,
          value: formData.rank,
          description: formData.name || `Reach Rank ${formData.rank}`,
        };
        break;
      case "trade_profit":
      case "daily_profit":
        newAchievement = {
          ...newAchievement,
          value: parseFloat(formData.amount),
          description:
            formData.name ||
            `${
              selectedCategory === "trade_profit"
                ? "Single Trade"
                : "Daily Profit"
            } $${formData.amount}`,
        };
        break;
      case "stability":
      case "discipline":
        newAchievement = {
          ...newAchievement,
          value: parseInt(formData.timeCount),
          timeUnit: formData.timeUnit,
          event: formData.event,
          description:
            formData.name ||
            `${formData.timeCount} ${formData.timeUnit} ${
              selectedCategory === "stability" ? "Profitable" : "Systematic"
            }`,
        };
        break;
    }

    setAchievements([...achievements, newAchievement]);
    setShowForm(false);
    setSelectedCategory(null);
  };

  const deleteAchievement = (id) => {
    setAchievements(achievements.filter((a) => a.id !== id));
  };

  const getAchievementsByCategory = (categoryId) => {
    return achievements.filter((a) => a.category === categoryId);
  };

  const getCurrentAchievementInCategory = (categoryAchievements) => {
    return (
      categoryAchievements.find((a) => !a.completed) ||
      categoryAchievements[categoryAchievements.length - 1]
    );
  };

  const getCategoryProgress = (categoryAchievements) => {
    // Находим текущую незавершенную ачивку
    const currentAchievement = categoryAchievements.find((a) => !a.completed);

    // Если есть незавершенная ачивка, показываем её прогресс
    if (currentAchievement) {
      if (currentAchievement.category === "levelup") {
        const rankOrder = ["F", "E", "D", "C", "B", "A", "S", "S+", "SSS"];
        const currentRankIndex = rankOrder.indexOf(rank.name);
        const targetRankIndex = rankOrder.indexOf(currentAchievement.value);

        if (currentRankIndex >= targetRankIndex) {
          return 100; // Цель достигнута
        } else {
          // Используем ту же логику что и в Dashboard для прогресса внутри ранга
          return rank.nextRank
            ? Math.round(
                ((balance - rank.minBalance) /
                  (rank.nextRank - rank.minBalance)) *
                  100
              )
            : 100;
        }
      } else {
        // Для остальных категорий: процентный прогресс
        const progress = Math.min(
          (currentAchievement.current / currentAchievement.value) * 100,
          100
        );
        // Обновляем прогресс в самой ачивке для использования в других местах
        currentAchievement.progress = progress;
        return progress;
      }
    }

    // Если все ачивки завершены, показываем 0% (готовность к новой ачивке)
    return 0;
  };

  const renderFormFields = () => {
    switch (selectedCategory) {
      case "levelup":
        return (
          <>
            <div>
              <label
                style={{
                  display: "block",
                  color: "#ffffff",
                  marginBottom: "8px",
                  fontWeight: "600",
                }}
              >
                Target Rank
              </label>
              <select
                value={formData.rank}
                onChange={(e) =>
                  setFormData({ ...formData, rank: e.target.value })
                }
                style={{ ...styles.input, color: "#ffffff" }}
              >
                {rankOptions.map((rank) => (
                  <option
                    key={rank}
                    value={rank}
                    style={{ backgroundColor: "#1a1a1a", color: "#ffffff" }}
                  >
                    {rank}
                  </option>
                ))}
              </select>
            </div>
          </>
        );

      case "trade_profit":
      case "daily_profit":
        return (
          <>
            <div>
              <label
                style={{
                  display: "block",
                  color: "#ffffff",
                  marginBottom: "8px",
                  fontWeight: "600",
                }}
              >
                Target Amount ($)
              </label>
              <input
                type="number"
                step="0.01"
                value={formData.amount}
                onChange={(e) =>
                  setFormData({ ...formData, amount: e.target.value })
                }
                placeholder="0"
                style={styles.input}
                required
              />
            </div>
          </>
        );

      case "stability":
      case "discipline":
        return (
          <>
            <div
              style={{
                display: "grid",
                gridTemplateColumns: "1fr 1fr",
                gap: "16px",
              }}
            >
              <div>
                <label
                  style={{
                    display: "block",
                    color: "#ffffff",
                    marginBottom: "8px",
                    fontWeight: "600",
                  }}
                >
                  Count
                </label>
                <input
                  type="number"
                  value={formData.timeCount}
                  onChange={(e) =>
                    setFormData({ ...formData, timeCount: e.target.value })
                  }
                  placeholder="0"
                  style={styles.input}
                  required
                />
              </div>
              <div>
                <label
                  style={{
                    display: "block",
                    color: "#ffffff",
                    marginBottom: "8px",
                    fontWeight: "600",
                  }}
                >
                  Time Unit
                </label>
                <select
                  value={formData.timeUnit}
                  onChange={(e) =>
                    setFormData({ ...formData, timeUnit: e.target.value })
                  }
                  style={{ ...styles.input, color: "#ffffff" }}
                >
                  {timeUnits.map((unit) => (
                    <option
                      key={unit}
                      value={unit}
                      style={{ backgroundColor: "#1a1a1a", color: "#ffffff" }}
                    >
                      {unit}
                    </option>
                  ))}
                </select>
              </div>
            </div>
          </>
        );

      default:
        return null;
    }
  };

  const groupedAchievements = achievements.reduce((acc, achievement) => {
    // Обновляем прогресс только если ачивка еще не завершена
    if (!achievement.completed) {
      achievement.current = updateAchievementProgress(achievement);
      const wasCompleted = achievement.completed;
      achievement.completed = checkAchievementCompletion(achievement);

      // Если ачивка только что завершилась, добавляем дату завершения
      if (
        !wasCompleted &&
        achievement.completed &&
        !achievement.dateCompleted
      ) {
        achievement.dateCompleted = new Date().toISOString().split("T")[0];
      }
    }
    if (!acc[achievement.category]) {
      acc[achievement.category] = [];
    }
    acc[achievement.category].push(achievement);
    return acc;
  }, {});

  return (
    <div className="space-y-6">
      <div className="flex justify-between items-center mb-8">
        <h1 className="text-4xl font-light text-white">Achievements</h1>
        <div className="text-right">
          <div className="text-sm text-white/60">Balance</div>
          <div
            className={`text-2xl font-semibold ${
              balance >= 0 ? "text-green-400" : "text-red-400"
            }`}
          >
            ${balance.toFixed(2)}
          </div>
        </div>
      </div>

      {/* Form Modal */}
      {showForm && (
        <div
          style={{
            position: "fixed",
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            backgroundColor: "rgba(0, 0, 0, 0.8)",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            zIndex: 1000,
          }}
          onClick={() => setShowForm(false)}
        >
          <div
            style={{
              ...styles.card,
              width: "90%",
              maxWidth: "500px",
              maxHeight: "90vh",
              overflowY: "auto",
            }}
            onClick={(e) => e.stopPropagation()}
          >
            <h3
              style={{
                fontSize: "1.3em",
                fontWeight: "700",
                marginBottom: "24px",
                color: "#ffffff",
              }}
            >
              Add new Achievement
            </h3>

            <form onSubmit={handleSubmit}>
              {renderFormFields()}

              <div
                style={{
                  display: "flex",
                  gap: "12px",
                  justifyContent: "flex-end",
                  marginTop: "24px",
                }}
              >
                <button
                  type="button"
                  onClick={() => setShowForm(false)}
                  style={{
                    padding: "12px 24px",
                    borderRadius: "8px",
                    border: "1px solid rgba(255, 255, 255, 0.2)",
                    background: "rgba(255, 255, 255, 0.1)",
                    color: "#ffffff",
                    cursor: "pointer",
                    fontWeight: "600",
                  }}
                >
                  Cancel
                </button>
                <button
                  type="submit"
                  style={{
                    padding: "12px 24px",
                    borderRadius: "8px",
                    border: "none",
                    background: "rgba(255, 255, 255, 0.2)",
                    color: "#ffffff",
                    cursor: "pointer",
                    fontWeight: "600",
                  }}
                >
                  Add
                </button>
              </div>
            </form>
          </div>
        </div>
      )}

      {/* Achievement Categories */}
      <div className="space-y-6">
        {Object.entries(achievementCategories).map(
          ([categoryKey, category]) => {
            const categoryAchievements = getAchievementsByCategory(categoryKey);
            const currentAchievement =
              getCurrentAchievementInCategory(categoryAchievements);
            const completedCount = categoryAchievements.filter(
              (a) => a.completed
            ).length;
            const progress = getCategoryProgress(categoryAchievements);

            return (
              <div key={categoryKey} style={styles.card}>
                <div
                  style={{
                    display: "flex",
                    alignItems: "center",
                    justifyContent: "space-between",
                    marginBottom: "20px",
                  }}
                >
                  <div
                    style={{
                      display: "flex",
                      alignItems: "center",
                      gap: "16px",
                    }}
                  >
                    <div
                      style={{
                        width: "60px",
                        height: "60px",
                        borderRadius: "12px",
                        background:
                          progress === 100
                            ? "linear-gradient(135deg, #00ff88, #00cc70)"
                            : "linear-gradient(135deg, #6a5acd, #9370db)",
                        display: "flex",
                        alignItems: "center",
                        justifyContent: "center",
                        fontSize: "2em",
                      }}
                    >
                      {category.icon}
                    </div>
                    <div>
                      <h3
                        style={{
                          fontWeight: "700",
                          fontSize: "1.4em",
                          marginBottom: "4px",
                          color: progress === 100 ? "#00ff88" : "#ffffff",
                        }}
                      >
                        {category.name}
                      </h3>
                      <p
                        style={{
                          color: "rgba(255, 255, 255, 0.7)",
                          fontSize: "0.9em",
                          margin: 0,
                        }}
                      >
                        {category.description}
                      </p>
                    </div>
                  </div>
                  <div
                    style={{
                      display: "flex",
                      alignItems: "center",
                      gap: "16px",
                    }}
                  >
                    <div style={{ textAlign: "right" }}>
                      <div
                        style={{
                          fontSize: "1.5em",
                          fontWeight: "700",
                          color: "#ffffff",
                        }}
                      >
                        {completedCount}/{categoryAchievements.length}
                      </div>
                      <div style={{ fontSize: "0.9em", opacity: 0.7 }}>
                        {Math.round(progress)}% completed
                      </div>
                    </div>
                    <button
                      onClick={() => openForm(categoryKey)}
                      style={{
                        background: "rgba(255, 255, 255, 0.2)",
                        color: "#ffffff",
                        border: "none",
                        padding: "12px",
                        borderRadius: "12px",
                        cursor: "pointer",
                        display: "flex",
                        alignItems: "center",
                        gap: "8px",
                      }}
                    >
                      <Plus size={20} />
                    </button>
                  </div>
                </div>

                {/* Current Achievement Progress */}
                {categoryAchievements.length > 0 && currentAchievement && (
                  <div
                    style={{
                      background: "rgba(255, 255, 255, 0.05)",
                      borderRadius: "12px",
                      padding: "20px",
                      marginBottom: "16px",
                    }}
                  >
                    <div style={{ marginBottom: "12px" }}>
                      <div style={{ fontWeight: "600", marginBottom: "4px" }}>
                        Current Goal: {currentAchievement.description}
                      </div>
                      <div style={{ fontSize: "0.9em", opacity: 0.7 }}>
                        Progress: {currentAchievement.current} /{" "}
                        {currentAchievement.value} ({Math.round(progress)}%)
                      </div>
                    </div>
                    <div
                      style={{
                        width: "100%",
                        height: "8px",
                        background: "rgba(255, 255, 255, 0.1)",
                        borderRadius: "4px",
                        overflow: "hidden",
                      }}
                    >
                      <div
                        style={{
                          height: "100%",
                          width: `${Math.min(progress, 100)}%`,
                          background:
                            "linear-gradient(90deg, #00ff88, #00cc70)",
                          borderRadius: "4px",
                          transition: "width 0.3s ease",
                        }}
                      />
                    </div>
                  </div>
                )}

                {/* All Achievements in Category */}
                {categoryAchievements.length > 0 ? (
                  <div
                    style={{ display: "flex", flexWrap: "wrap", gap: "8px" }}
                  >
                    {categoryAchievements.map((achievement) => (
                      <div
                        key={achievement.id}
                        style={{
                          background: achievement.completed
                            ? "linear-gradient(135deg, rgba(0, 255, 136, 0.2), rgba(0, 255, 136, 0.1))"
                            : achievement === currentAchievement
                            ? "linear-gradient(135deg, rgba(136, 136, 136, 0.2), rgba(136, 136, 136, 0.1))"
                            : "rgba(255, 255, 255, 0.05)",
                          border: achievement.completed
                            ? "1px solid #00ff88"
                            : achievement === currentAchievement
                            ? "1px solid #888888"
                            : "1px solid rgba(255, 255, 255, 0.1)",
                          borderRadius: "8px",
                          padding: "12px 16px",
                          position: "relative",
                          minWidth: "120px",
                          textAlign: "center",
                        }}
                      >
                        <button
                          onClick={() => deleteAchievement(achievement.id)}
                          style={{
                            position: "absolute",
                            top: "4px",
                            right: "4px",
                            background: "rgba(255, 107, 107, 0.2)",
                            border: "none",
                            borderRadius: "4px",
                            padding: "4px",
                            color: "#ff6b6b",
                            cursor: "pointer",
                          }}
                        >
                          <Trash2 size={12} />
                        </button>
                        <div
                          style={{
                            fontWeight: "600",
                            fontSize: "0.9em",
                            color: achievement.completed
                              ? "#00ff88"
                              : "#ffffff",
                            marginBottom: "4px",
                          }}
                        >
                          {achievement.category === "levelup"
                            ? `Rank ${achievement.value}`
                            : achievement.category === "discipline" &&
                              achievement.event
                            ? achievement.event
                            : achievement.category === "stability" ||
                              achievement.category === "discipline"
                            ? `${achievement.value} ${achievement.timeUnit}`
                            : `$${achievement.value}`}
                        </div>
                        {achievement.completed && (
                          <div style={{ color: "#00ff88", fontSize: "0.8em" }}>
                            ✓ Completed
                          </div>
                        )}
                        {achievement === currentAchievement &&
                          !achievement.completed && (
                            <div
                              style={{ color: "#ffffff", fontSize: "0.8em" }}
                            >
                              Current Goal
                            </div>
                          )}
                      </div>
                    ))}
                  </div>
                ) : (
                  <div style={{ textAlign: "center", padding: "40px" }}>
                    <h3
                      style={{
                        fontSize: "1.2em",
                        color: "rgba(255, 255, 255, 0.5)",
                        marginBottom: "8px",
                      }}
                    >
                      No achievements yet
                    </h3>
                    <p
                      style={{
                        color: "rgba(255, 255, 255, 0.4)",
                        fontSize: "0.9em",
                      }}
                    ></p>
                  </div>
                )}
              </div>
            );
          }
        )}
      </div>
    </div>
  );
};

export default Achievements;
