import React from "react";
import {
  LayoutDashboard,
  TrendingUp,
  FileText,
  Trophy,
  CheckSquare,
  StickyNote,
  Settings,
  ChevronRight,
  ChevronLeft,
} from "lucide-react";
import { useAuth } from "../hooks/useAuth";

const Sidebar = ({
  activeSection,
  setActiveSection,
  isCollapsed,
  setIsCollapsed,
}) => {
  const menuItems = [
    { id: "dashboard", label: "Dashboard", icon: LayoutDashboard },
    { id: "trades", label: "Trades", icon: TrendingUp },
    { id: "reports", label: "Reports", icon: FileText },
    { id: "achievements", label: "Achievements", icon: Trophy },
    { id: "tasks", label: "Tasks", icon: CheckSquare },
    { id: "notes", label: "Notes", icon: StickyNote },
    { id: "settings", label: "Settings", icon: Settings },
  ];
  const { user } = useAuth();

  return (
    <div
      className={`${
        isCollapsed ? "w-20" : "w-80"
      } flex flex-col border-r border-white/10 transition-all duration-300`}
      style={{
        background:
          "linear-gradient(135deg, rgba(255, 255, 255, 0.05), rgba(255, 255, 255, 0.02))",
        backdropFilter: "blur(20px)",
      }}
    >
      {/* Заголовок приложения */}
      <div
        className="p-8 border-b border-white/10 relative overflow-hidden cursor-pointer"
        style={{
          background:
            "linear-gradient(135deg, rgba(255, 255, 255, 0.05), rgba(255, 255, 255, 0.02))",
          backdropFilter: "blur(20px)",
        }}
      >
        <div className="flex items-center justify-between relative z-10">
          <div className="flex items-center space-x-3">
            {!isCollapsed && (
              <h1 className="text-2xl font-bold text-white">Solo Leveling</h1>
            )}
            {/* Индикатор статуса аутентификации */}
            <div className="flex items-center space-x-2">
              <div
                className={`w-2 h-2 rounded-full ${
                  user ? "bg-green-400" : "bg-red-400"
                } animate-pulse`}
              />
              {!isCollapsed && (
                <span className="text-xs text-white/60">
                  {user ? "Online" : "Offline"}
                </span>
              )}
            </div>
          </div>
          <button
            onClick={() => setIsCollapsed(!isCollapsed)}
            className="p-2 rounded-lg text-white/80 hover:text-white hover:bg-white/10 transition-all duration-300"
          >
            <ChevronLeft
              className={`w-5 h-5 transition-transform duration-300 ${
                isCollapsed ? "rotate-180" : ""
              }`}
            />
          </button>
        </div>
      </div>

      {/* Навигационное меню */}
      <nav className="flex-1 p-6">
        <div className="space-y-3">
          {menuItems.map((item) => {
            const Icon = item.icon;
            const isActive = activeSection === item.id;

            return (
              <button
                key={item.id}
                onClick={() => setActiveSection(item.id)}
                className={`w-full flex items-center ${
                  isCollapsed ? "justify-center px-1" : "space-x-4 px-6"
                } py-4 rounded-xl transition-all duration-300 group ${
                  isActive
                    ? "text-white font-semibold bg-white/10"
                    : "text-white/80 hover:text-white hover:bg-white/5"
                }`}
              >
                <Icon
                  className={`w-5 h-5 ${isCollapsed ? "text-white" : ""}`}
                />
                {!isCollapsed && (
                  <span className="font-medium">{item.label}</span>
                )}
                {!isCollapsed && isActive && (
                  <ChevronRight className="w-4 h-4 ml-auto" />
                )}
              </button>
            );
          })}
        </div>
      </nav>
    </div>
  );
};

export default Sidebar;
