import React, { useState } from "react";
import { CheckSquare, Plus, Trash2, Edit3 } from "lucide-react";

const Tasks = ({ balance, tasks, setTasks, taskHistory = {} }) => {
  // Добавьте эти строки после объявления компонента:
  // Убери эту строку полностью, так как tasks теперь приходят через пропсы
  const [showForm, setShowForm] = useState(false);
  const [newTask, setNewTask] = useState("");
  const [editingTask, setEditingTask] = useState(null);
  const [selectedDate, setSelectedDate] = useState(null);
  const [showHistoryModal, setShowHistoryModal] = useState(false);
  // Добавьте после useState:
  const generateCalendarDays = () => {
    const today = new Date();
    const dayOfWeek = today.getDay();
    const mondayOffset = dayOfWeek === 0 ? -6 : 1 - dayOfWeek;
    const monday = new Date(today);
    monday.setDate(today.getDate() + mondayOffset);

    const days = [];

    for (let i = 0; i < 7; i++) {
      const date = new Date(monday);
      date.setDate(monday.getDate() + i);
      const dateString = date.toDateString();
      const isToday = dateString === today.toDateString();

      // Получаем задачи для этого дня
      const dayTasks = isToday ? tasks : taskHistory[dateString] || [];

      days.push({
        date: date.getDate(),
        day: date.toLocaleDateString("en", { weekday: "short" }),
        totalTasks: dayTasks.length,
        completedTasks: dayTasks.filter((t) => t.completed).length,
        isToday: isToday,
        dateString: dateString,
        hasTasks: dayTasks.length > 0,
      });
    }
    return days;
  };

  const calendarDays = generateCalendarDays();

  const addTask = () => {
    if (newTask.trim()) {
      setTasks([...tasks, { id: Date.now(), text: newTask, completed: false }]);
      setNewTask("");
      setShowForm(false);
    }
  };

  const toggleTask = (id) => {
    setTasks(
      tasks.map((task) =>
        task.id === id ? { ...task, completed: !task.completed } : task
      )
    );
  };

  const deleteTask = (id) => {
    setTasks(tasks.filter((task) => task.id !== id));
  };
  const editTask = (task) => {
    setEditingTask(task);
    setNewTask(task.text);
    setShowForm(true);
  };

  const saveEdit = () => {
    if (newTask.trim()) {
      setTasks(
        tasks.map((task) =>
          task.id === editingTask.id ? { ...task, text: newTask } : task
        )
      );
      setNewTask("");
      setShowForm(false);
      setEditingTask(null);
    }
  };

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex justify-between items-center mb-8">
        <h1 className="text-4xl font-light text-white">Tasks</h1>
        <div className="text-right">
          <div className="text-sm text-white/60">Balance</div>
          <div
            className={`text-2xl font-semibold ${
              balance >= 0 ? "text-green-400" : "text-red-400"
            }`}
          >
            ${balance.toFixed(2)}
          </div>
        </div>
      </div>

      {showForm && (
        <div
          style={{
            position: "fixed",
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            backgroundColor: "rgba(0, 0, 0, 0.8)",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            zIndex: 1000,
          }}
          onClick={() => {
            setShowForm(false);
            setEditingTask(null);
          }}
        >
          <div
            className="p-6 rounded-2xl border border-white/10 w-96"
            style={{
              background:
                "linear-gradient(135deg, rgba(255, 255, 255, 0.08), rgba(255, 255, 255, 0.02))",
              backdropFilter: "blur(20px)",
            }}
            onClick={(e) => e.stopPropagation()}
          >
            <h3 className="text-lg font-medium text-white mb-4">
              {editingTask ? "Edit Task" : "Add new Task"}
            </h3>
            <textarea
              value={newTask}
              onChange={(e) => setNewTask(e.target.value)}
              placeholder="Enter task..."
              className="w-full p-3 bg-white/10 border border-white/20 rounded-lg text-white placeholder-gray-400 mb-4 resize-none"
              style={{
                minHeight: "48px",
                maxHeight: "200px",
                height: "auto",
              }}
              onInput={(e) => {
                e.target.style.height = "auto";
                e.target.style.height = e.target.scrollHeight + "px";
              }}
              onKeyPress={(e) => {
                if (e.key === "Enter" && !e.shiftKey) {
                  e.preventDefault();
                  editingTask ? saveEdit() : addTask();
                }
              }}
              autoFocus
            />
            <div className="flex gap-2 justify-end">
              <button
                onClick={() => {
                  setShowForm(false);
                  setEditingTask(null);
                  setNewTask("");
                }}
                style={{
                  padding: "8px 16px",
                  borderRadius: "8px",
                  border: "none",
                  background: "rgba(255, 255, 255, 0.1)",
                  color: "#ffffff",
                  cursor: "pointer",
                  fontWeight: "600",
                  transition: "all 0.2s ease",
                }}
              >
                Cancel
              </button>
              <button
                onClick={editingTask ? saveEdit : addTask}
                style={{
                  padding: "8px 16px",
                  borderRadius: "8px",
                  border: "none",
                  background: "rgba(255, 255, 255, 0.2)",
                  color: "#ffffff",
                  cursor: "pointer",
                  fontWeight: "600",
                  transition: "all 0.2s ease",
                }}
              >
                {editingTask ? "Save" : "Add"}
              </button>
            </div>
          </div>
        </div>
      )}

      {/* Calendar */}
      <div
        className="p-6 rounded-2xl border border-white/10"
        style={{
          background:
            "linear-gradient(135deg, rgba(255, 255, 255, 0.08), rgba(255, 255, 255, 0.02))",
          backdropFilter: "blur(20px)",
        }}
      >
        <div className="mb-6">
          <h2 className="text-xl font-medium text-white">
            {`${new Date().toLocaleDateString("en", {
              month: "long",
            })}, ${new Date().getFullYear()}`}
          </h2>
        </div>

        <div className="grid grid-cols-7 gap-2">
          {calendarDays.map((day) => (
            <div
              key={day.date}
              className={`p-3 rounded-lg cursor-pointer transition-colors ${
                day.isToday
                  ? "bg-white/10 border border-white/20"
                  : day.hasTasks
                  ? "bg-white/5 hover:bg-white/10"
                  : "bg-white/5"
              }`}
              onClick={() => {
                if (day.hasTasks) {
                  setSelectedDate(day.dateString);
                  setShowHistoryModal(true);
                }
              }}
            >
              <div className="flex items-center gap-2 mb-2">
                <div className="text-lg font-medium text-white">{day.date}</div>
                <div className="text-xs text-gray-400">{day.day}</div>
              </div>
              <div
                className={`text-sm font-medium ${
                  day.totalTasks > 0 && day.completedTasks === day.totalTasks
                    ? "text-green-400"
                    : "text-white"
                }`}
              >
                {day.completedTasks}/{day.totalTasks}
              </div>
              <div className="text-xs text-gray-400">tasks</div>
            </div>
          ))}
        </div>
      </div>

      {/* Tasks List */}
      <div
        className="p-6 rounded-2xl border border-white/10"
        style={{
          background:
            "linear-gradient(135deg, rgba(255, 255, 255, 0.08), rgba(255, 255, 255, 0.02))",
          backdropFilter: "blur(20px)",
        }}
      >
        <div className="flex justify-between items-center mb-6">
          <h2 className="text-xl font-medium text-white">Daily Tasks</h2>
          <button
            onClick={() => setShowForm(!showForm)}
            className="p-2 bg-white/20 hover:bg-white/30 rounded-lg transition-colors"
          >
            <Plus size={20} className="text-white" />
          </button>
        </div>

        {tasks.length === 0 ? (
          <p className="text-center text-white/50 py-8">
            No tasks for today yet
          </p>
        ) : (
          <div className="space-y-3">
            {tasks.map((task) => (
              <div
                key={task.id}
                className={`flex items-start gap-3 p-4 rounded-lg ${
                  task.completed ? "bg-green-500/10" : "bg-white/5"
                }`}
              >
                <button
                  onClick={() => toggleTask(task.id)}
                  className={`w-5 h-5 rounded border-2 flex items-center justify-center ${
                    task.completed
                      ? "bg-green-500 border-green-500"
                      : "border-white/30"
                  }`}
                >
                  {task.completed && (
                    <CheckSquare size={14} className="text-white" />
                  )}
                </button>
                <span
                  className={`flex-1 leading-relaxed ${
                    task.completed ? "text-white/60 line-through" : "text-white"
                  }`}
                  style={{
                    wordBreak: "break-word",
                    whiteSpace: "pre-wrap",
                    overflowWrap: "break-word",
                  }}
                >
                  {task.text}
                </span>
                <div className="flex gap-2">
                  <button
                    onClick={() => editTask(task)}
                    className="p-1 text-white/60 hover:text-white transition-colors"
                  >
                    <Edit3 size={16} />
                  </button>
                  <button
                    onClick={() => deleteTask(task.id)}
                    className="p-1 text-red-400 hover:text-red-300 transition-colors"
                  >
                    <Trash2 size={16} />
                  </button>
                </div>
              </div>
            ))}
          </div>
        )}
      </div>
      {/* History Modal */}
      {showHistoryModal && selectedDate && (
        <div
          style={{
            position: "fixed",
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            backgroundColor: "rgba(0, 0, 0, 0.8)",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            zIndex: 1000,
          }}
          onClick={() => setShowHistoryModal(false)}
        >
          <div
            className="p-6 rounded-2xl border border-white/10 w-96 max-h-96 overflow-y-auto"
            style={{
              background:
                "linear-gradient(135deg, rgba(255, 255, 255, 0.08), rgba(255, 255, 255, 0.02))",
              backdropFilter: "blur(20px)",
            }}
            onClick={(e) => e.stopPropagation()}
          >
            <h3 className="text-lg font-medium text-white mb-4">
              Tasks for {new Date(selectedDate).toLocaleDateString()}
            </h3>

            {/* Показываем задачи для выбранного дня */}
            {(() => {
              const isToday = selectedDate === new Date().toDateString();
              const dayTasks = isToday
                ? tasks
                : taskHistory[selectedDate] || [];

              return dayTasks.length > 0 ? (
                <div className="space-y-3">
                  {dayTasks.map((task) => (
                    <div
                      key={task.id}
                      className={`flex items-start gap-3 p-3 rounded-lg ${
                        task.completed ? "bg-green-500/10" : "bg-white/5"
                      }`}
                    >
                      <div
                        className={`w-5 h-5 rounded border-2 flex items-center justify-center ${
                          task.completed
                            ? "bg-green-500 border-green-500"
                            : "border-white/30"
                        }`}
                      >
                        {task.completed && (
                          <CheckSquare size={14} className="text-white" />
                        )}
                      </div>
                      <span
                        className={`flex-1 leading-relaxed ${
                          task.completed
                            ? "text-white/60 line-through"
                            : "text-white"
                        }`}
                        style={{
                          wordBreak: "break-word",
                          whiteSpace: "pre-wrap",
                          overflowWrap: "break-word",
                        }}
                      >
                        {task.text}
                      </span>
                    </div>
                  ))}
                </div>
              ) : (
                <p className="text-center text-white/50 py-4">
                  No tasks for this day
                </p>
              );
            })()}

            <button
              onClick={() => setShowHistoryModal(false)}
              className="w-full mt-4 p-2 bg-white/20 hover:bg-white/30 rounded-lg text-white transition-colors"
            >
              Close
            </button>
          </div>
        </div>
      )}
    </div>
  );
};

export default Tasks;
