import { useState, useEffect } from "react";

// Проверяем, работаем ли мы в Electron
const isElectron = () => {
  return !!(window && window.process && window.process.type);
};

// Если Electron недоступен, используем localStorage как fallback
const createStore = () => {
  try {
    if (window && window.process && window.process.type) {
      const Store = window.require("electron-store");
      return new Store();
    }
  } catch (error) {
    console.log("Electron не найден, используем localStorage");
  }

  // Fallback для браузера
  return {
    get: (key, defaultValue) => {
      try {
        const stored = localStorage.getItem(key);
        return stored ? JSON.parse(stored) : defaultValue;
      } catch {
        return defaultValue;
      }
    },
    set: (key, value) => {
      try {
        localStorage.setItem(key, JSON.stringify(value));
      } catch (error) {
        console.error("Ошибка сохранения:", error);
      }
    },
  };
};

const store = createStore();

export const useDataStore = () => {
  // Функция для загрузки всех данных
  const loadData = () => {
    return {
      trades: store.get("trades", []),
      achievements: store.get("achievements", []),
      dailyResults: store.get("dailyResults", []),
      tasks: store.get("tasks", []),
      taskHistory: store.get("taskHistory", {}),
      userProfile: store.get("userProfile", {
        avatar: "👨‍💼",
        nickname: "TradeMaster",
      }),
      notes: store.get("notes", []),
    };
  };

  // Функция для сохранения данных
  const saveData = (key, value) => {
    store.set(key, value);
  };

  // Функция для сохранения всех данных сразу
  const saveAllData = (data) => {
    Object.keys(data).forEach((key) => {
      if (data[key] !== undefined) {
        store.set(key, data[key]);
      }
    });
  };

  // Функция для очистки всех данных
  const clearAllData = () => {
    const keys = [
      "trades",
      "achievements",
      "dailyResults",
      "tasks",
      "taskHistory",
      "userProfile",
      "notes",
    ];
    keys.forEach((key) => {
      store.set(
        key,
        key === "userProfile" ? { avatar: "👨‍💼", nickname: "TradeMaster" } : []
      );
    });
  };

  return {
    loadData,
    saveData,
    saveAllData,
    clearAllData,
  };
};
