import React, { useState, useEffect } from "react";
import {
  DollarSign,
  TrendingUp,
  TrendingDown,
  Edit3,
  Trash2,
  Plus,
} from "lucide-react";

const TradingJournal = ({ trades, setTrades, balance, onTradesChange }) => {
  const [showForm, setShowForm] = useState(false);
  const [editingTrade, setEditingTrade] = useState(null);
  const [formData, setFormData] = useState({
    date: new Date().toISOString().split("T")[0],
    time: new Date().toLocaleTimeString("ru-RU", {
      hour: "2-digit",
      minute: "2-digit",
    }),
    type: "trade",
    ticker: "",
    side: "Long",
    entryReasons: "",
    result: "",
    commission: "",
    comment: "",
    isSystematic: "Да",
    operationType: "deposit",
    amount: "",
  });
  useEffect(() => {
    if (onTradesChange) {
      onTradesChange(trades);
    }
  }, [trades, onTradesChange]);

  const styles = {
    card: {
      background:
        "linear-gradient(135deg, rgba(255, 255, 255, 0.08), rgba(255, 255, 255, 0.02))",
      backdropFilter: "blur(20px)",
      borderRadius: "16px",
      border: "1px solid rgba(255, 255, 255, 0.1)",
      padding: "24px",
    },
    button: {
      padding: "12px 24px",
      borderRadius: "8px",
      border: "none",
      fontWeight: "600",
      cursor: "pointer",
      transition: "all 0.2s ease",
      display: "inline-flex",
      alignItems: "center",
      gap: "8px",
    },
    input: {
      width: "100%",
      padding: "12px",
      background: "rgba(255, 255, 255, 0.1)",
      border: "1px solid rgba(255, 255, 255, 0.2)",
      borderRadius: "8px",
      color: "#ffffff",
      fontSize: "14px",
    },
  };

  const formatDate = (dateString) => {
    const date = new Date(dateString);
    return date.toLocaleDateString("ru-RU");
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    const newTrade = {
      id: editingTrade ? editingTrade.id : Date.now(),
      ...formData,
      result: formData.result ? parseFloat(formData.result) : 0,
      commission: formData.commission ? parseFloat(formData.commission) : 0,
      amount: formData.amount ? parseFloat(formData.amount) : 0,
    };

    if (editingTrade) {
      setTrades(
        trades.map((trade) => (trade.id === editingTrade.id ? newTrade : trade))
      );
    } else {
      setTrades([newTrade, ...trades]);
    }

    // Reset form
    setFormData({
      date: new Date().toISOString().split("T")[0],
      time: new Date().toLocaleTimeString("ru-RU", {
        hour: "2-digit",
        minute: "2-digit",
      }),
      type: "trade",
      ticker: "",
      side: "Long",
      entryReasons: "",
      result: "",
      commission: "",
      comment: "",
      isSystematic: "Да",
      operationType: "deposit",
      amount: "",
    });
    setShowForm(false);
    setEditingTrade(null);
  };

  const deleteTrade = (id) => {
    setTrades(trades.filter((trade) => trade.id !== id));
  };

  const editTrade = (trade) => {
    setFormData({
      ...trade,
      result: trade.result?.toString() || "",
      commission: trade.commission?.toString() || "",
      amount: trade.amount?.toString() || "",
    });
    setEditingTrade(trade);
    setShowForm(true);
  };
  const calculateBalance = () => {
    return trades.reduce((total, trade) => {
      if (trade.type === "deposit_operation") {
        return trade.operationType === "deposit"
          ? total + trade.amount
          : total - trade.amount;
      } else {
        return total + trade.result - trade.commission;
      }
    }, 0);
  };

  return (
    <div className="space-y-0">
      {/* Header */}

      <div className="flex justify-between items-center mb-8">
        <h1 className="text-4xl font-light text-white">Trades</h1>
        <div className="text-right">
          <div className="text-sm text-white/60">Balance</div>
          <div className="text-2xl font-semibold text-green-400">
            ${balance.toFixed(2)}
          </div>
        </div>
      </div>

      {/* Form */}

      {showForm && (
        <div
          style={{
            position: "fixed",
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            backgroundColor: "rgba(0, 0, 0, 0.8)",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            zIndex: 1000,
          }}
          onClick={() => {
            setShowForm(false);
            setEditingTrade(null);
          }}
        >
          <div
            style={{
              ...styles.card,
              width: "90%",
              maxWidth: "600px",
              maxHeight: "90vh",
              overflowY: "auto",
            }}
            onClick={(e) => e.stopPropagation()}
          >
            <h3
              style={{
                fontSize: "1.3em",
                fontWeight: "700",
                marginBottom: "24px",
                color: "#ffffff",
              }}
            >
              {editingTrade ? "Edit Trade" : "Add new Trade"}
            </h3>

            <div>
              {formData.type === "trade" ? (
                <div
                  style={{
                    display: "grid",
                    gridTemplateColumns: "repeat(auto-fit, minmax(250px, 1fr))",
                    gap: "16px",
                    marginBottom: "16px",
                  }}
                >
                  <div>
                    <label
                      style={{
                        display: "block",
                        color: "#ffffff",
                        marginBottom: "8px",
                      }}
                    >
                      Date
                    </label>
                    <input
                      type="date"
                      value={formData.date}
                      onChange={(e) =>
                        setFormData({ ...formData, date: e.target.value })
                      }
                      style={styles.input}
                      required
                    />
                  </div>

                  <div>
                    <label
                      style={{
                        display: "block",
                        color: "#ffffff",
                        marginBottom: "8px",
                      }}
                    >
                      Ticker
                    </label>
                    <input
                      type="text"
                      value={formData.ticker}
                      onChange={(e) =>
                        setFormData({ ...formData, ticker: e.target.value })
                      }
                      placeholder="AAPL, BTCUSDT..."
                      style={styles.input}
                      required
                    />
                  </div>

                  <div>
                    <label
                      style={{
                        display: "block",
                        color: "#ffffff",
                        marginBottom: "8px",
                      }}
                    >
                      Side
                    </label>
                    <select
                      value={formData.side}
                      onChange={(e) =>
                        setFormData({ ...formData, side: e.target.value })
                      }
                      style={{ ...styles.input, color: "#ffffff" }}
                    >
                      <option
                        value="Long"
                        style={{ backgroundColor: "#333", color: "#ffffff" }}
                      >
                        Long
                      </option>
                      <option
                        value="Short"
                        style={{ backgroundColor: "#333", color: "#ffffff" }}
                      >
                        Short
                      </option>
                    </select>
                  </div>

                  <div>
                    <label
                      style={{
                        display: "block",
                        color: "#ffffff",
                        marginBottom: "8px",
                      }}
                    >
                      System Trade
                    </label>
                    <select
                      value={formData.isSystematic}
                      onChange={(e) =>
                        setFormData({
                          ...formData,
                          isSystematic: e.target.value,
                        })
                      }
                      style={{ ...styles.input, color: "#ffffff" }}
                    >
                      <option
                        value="Да"
                        style={{ backgroundColor: "#333", color: "#ffffff" }}
                      >
                        Yes
                      </option>
                      <option
                        value="Нет"
                        style={{ backgroundColor: "#333", color: "#ffffff" }}
                      >
                        No
                      </option>
                    </select>
                  </div>

                  <div>
                    <label
                      style={{
                        display: "block",
                        color: "#ffffff",
                        marginBottom: "8px",
                      }}
                    >
                      Result ($)
                    </label>
                    <input
                      type="number"
                      step="0.01"
                      value={formData.result}
                      onChange={(e) =>
                        setFormData({ ...formData, result: e.target.value })
                      }
                      style={styles.input}
                      required
                    />
                  </div>

                  <div>
                    <label
                      style={{
                        display: "block",
                        color: "#ffffff",
                        marginBottom: "8px",
                      }}
                    >
                      Commission ($)
                    </label>
                    <input
                      type="number"
                      step="0.01"
                      value={formData.commission}
                      onChange={(e) =>
                        setFormData({ ...formData, commission: e.target.value })
                      }
                      style={styles.input}
                    />
                  </div>

                  <div style={{ gridColumn: "1 / -1" }}>
                    <label
                      style={{
                        display: "block",
                        color: "#ffffff",
                        marginBottom: "8px",
                      }}
                    >
                      Entry Reasons
                    </label>
                    <textarea
                      value={formData.entryReasons}
                      onChange={(e) =>
                        setFormData({
                          ...formData,
                          entryReasons: e.target.value,
                        })
                      }
                      placeholder="Describe why you entered this trade..."
                      rows={3}
                      style={{ ...styles.input, resize: "vertical" }}
                      required
                    />
                  </div>
                </div>
              ) : (
                <div
                  style={{
                    display: "grid",
                    gridTemplateColumns: "repeat(auto-fit, minmax(250px, 1fr))",
                    gap: "16px",
                    marginBottom: "16px",
                  }}
                >
                  <div>
                    <label
                      style={{
                        display: "block",
                        color: "#ffffff",
                        marginBottom: "8px",
                      }}
                    >
                      Date
                    </label>
                    <input
                      type="date"
                      value={formData.date}
                      onChange={(e) =>
                        setFormData({ ...formData, date: e.target.value })
                      }
                      style={styles.input}
                      required
                    />
                  </div>

                  <div>
                    <label
                      style={{
                        display: "block",
                        color: "#ffffff",
                        marginBottom: "8px",
                      }}
                    >
                      Operation Type
                    </label>
                    <select
                      value={formData.operationType}
                      onChange={(e) =>
                        setFormData({
                          ...formData,
                          operationType: e.target.value,
                        })
                      }
                      style={{ ...styles.input, color: "#ffffff" }}
                    >
                      <option
                        value="deposit"
                        style={{ backgroundColor: "#333", color: "#ffffff" }}
                      >
                        Deposit
                      </option>
                      <option
                        value="withdrawal"
                        style={{ backgroundColor: "#333", color: "#ffffff" }}
                      >
                        Withdrawal
                      </option>
                    </select>
                  </div>

                  <div>
                    <label
                      style={{
                        display: "block",
                        color: "#ffffff",
                        marginBottom: "8px",
                      }}
                    >
                      Amount ($)
                    </label>
                    <input
                      type="number"
                      step="0.01"
                      value={formData.amount}
                      onChange={(e) =>
                        setFormData({ ...formData, amount: e.target.value })
                      }
                      style={styles.input}
                      required
                    />
                  </div>
                </div>
              )}

              {formData.type === "trade" && (
                <div style={{ marginBottom: "24px" }}>
                  <label
                    style={{
                      display: "block",
                      color: "#ffffff",
                      marginBottom: "8px",
                    }}
                  >
                    Comment
                  </label>
                  <textarea
                    value={formData.comment}
                    onChange={(e) =>
                      setFormData({ ...formData, comment: e.target.value })
                    }
                    placeholder="Additional notes..."
                    rows={2}
                    style={{ ...styles.input, resize: "vertical" }}
                  />
                </div>
              )}

              <div
                style={{
                  display: "flex",
                  gap: "12px",
                  justifyContent: "flex-end",
                }}
              >
                <button
                  type="button"
                  onClick={() => {
                    setShowForm(false);
                    setEditingTrade(null);
                  }}
                  style={{
                    ...styles.button,
                    background: "rgba(255, 255, 255, 0.1)",
                    color: "#ffffff",
                  }}
                >
                  Cancel
                </button>
                <button
                  type="button"
                  onClick={handleSubmit}
                  style={{
                    ...styles.button,
                    background: "rgba(255, 255, 255, 0.2)",
                    color: "#ffffff",
                  }}
                >
                  {editingTrade ? "Save" : "Add"}
                </button>
              </div>
            </div>
          </div>
        </div>
      )}

      {/* Trades List */}
      <div style={styles.card}>
        <div
          style={{
            display: "flex",
            justifyContent: "flex-end",
            marginBottom: "24px",
          }}
        >
          <div style={{ display: "flex", gap: "12px" }}>
            <button
              onClick={() => {
                setFormData({ ...formData, type: "trade" });
                setShowForm(true);
              }}
              style={{
                ...styles.button,
                background: "rgba(255, 255, 255, 0.2)",
                color: "#ffffff",
                padding: "12px",
              }}
            >
              <Plus size={18} />
            </button>
            <button
              onClick={() => {
                setFormData({ ...formData, type: "deposit_operation" });
                setShowForm(true);
              }}
              style={{
                ...styles.button,
                background: "rgba(255, 255, 255, 0.2)",
                color: "#ffffff",
                padding: "12px",
              }}
            >
              <DollarSign size={18} />
            </button>
          </div>
        </div>

        {trades.length === 0 ? (
          <p
            style={{
              textAlign: "center",
              color: "rgba(255, 255, 255, 0.5)",
              padding: "32px",
            }}
          >
            No trades yet
          </p>
        ) : (
          <div style={{ overflowX: "auto" }}>
            <table style={{ width: "100%", borderCollapse: "collapse" }}>
              <thead>
                <tr
                  style={{ borderBottom: "2px solid rgba(255, 255, 255, 0.1)" }}
                >
                  <th
                    style={{
                      padding: "12px 8px",
                      textAlign: "left",
                      color: "#ffffff",
                      fontWeight: "600",
                      fontSize: "0.9em",
                    }}
                  >
                    Date/Time
                  </th>
                  <th
                    style={{
                      padding: "12px 8px",
                      textAlign: "left",
                      color: "#ffffff",
                      fontWeight: "600",
                      fontSize: "0.9em",
                    }}
                  >
                    Type
                  </th>
                  <th
                    style={{
                      padding: "12px 8px",
                      textAlign: "left",
                      color: "#ffffff",
                      fontWeight: "600",
                      fontSize: "0.9em",
                    }}
                  >
                    Ticker
                  </th>
                  <th
                    style={{
                      padding: "12px 8px",
                      textAlign: "center",
                      color: "#ffffff",
                      fontWeight: "600",
                      fontSize: "0.9em",
                    }}
                  >
                    Side
                  </th>
                  <th
                    style={{
                      padding: "12px 8px",
                      textAlign: "left",
                      color: "#ffffff",
                      fontWeight: "600",
                      fontSize: "0.9em",
                    }}
                  >
                    Entry Reasons
                  </th>
                  <th
                    style={{
                      padding: "12px 8px",
                      textAlign: "right",
                      color: "#ffffff",
                      fontWeight: "600",
                      fontSize: "0.9em",
                    }}
                  >
                    Result
                  </th>
                  <th
                    style={{
                      padding: "12px 8px",
                      textAlign: "right",
                      color: "#ffffff",
                      fontWeight: "600",
                      fontSize: "0.9em",
                    }}
                  >
                    Commission
                  </th>
                  <th
                    style={{
                      padding: "12px 8px",
                      textAlign: "center",
                      color: "#ffffff",
                      fontWeight: "600",
                      fontSize: "0.9em",
                    }}
                  >
                    System
                  </th>
                  <th
                    style={{
                      padding: "12px 8px",
                      textAlign: "center",
                      color: "#ffffff",
                      fontWeight: "600",
                      fontSize: "0.9em",
                    }}
                  >
                    Actions
                  </th>
                </tr>
              </thead>
              <tbody>
                {trades
                  .sort((a, b) => new Date(b.date) - new Date(a.date))
                  .map((item) => (
                    <tr
                      key={item.id}
                      style={{
                        borderBottom: "1px solid rgba(255, 255, 255, 0.05)",
                        transition: "background-color 0.2s ease",
                      }}
                    >
                      <td
                        style={{
                          padding: "16px 8px",
                          fontSize: "0.85em",
                          color: "rgba(255, 255, 255, 0.7)",
                        }}
                      >
                        <div>{formatDate(item.date)}</div>
                        <div>{item.time}</div>
                      </td>
                      <td style={{ padding: "16px 8px", fontWeight: "600" }}>
                        {item.type === "deposit_operation" ? (
                          <span
                            style={{
                              display: "inline-flex",
                              alignItems: "center",
                              gap: "4px",
                              padding: "4px 8px",
                              borderRadius: "6px",
                              fontSize: "0.8em",
                              fontWeight: "600",
                              background:
                                item.operationType === "deposit"
                                  ? "rgba(0, 255, 136, 0.2)"
                                  : "rgba(255, 107, 107, 0.2)",
                              color:
                                item.operationType === "deposit"
                                  ? "#00ff88"
                                  : "#ff6b6b",
                            }}
                          >
                            <DollarSign size={12} />
                            {item.operationType === "deposit"
                              ? "Пополнение"
                              : "Вывод"}
                          </span>
                        ) : (
                          <span
                            style={{
                              display: "inline-flex",
                              alignItems: "center",
                              gap: "4px",
                              padding: "4px 8px",
                              borderRadius: "6px",
                              fontSize: "0.8em",
                              fontWeight: "600",
                              background: "rgba(255, 255, 255, 0.1)",
                              color: "rgba(255, 255, 255, 0.8)",
                            }}
                          >
                            <TrendingUp size={12} />
                            Сделка
                          </span>
                        )}
                      </td>
                      <td
                        style={{
                          padding: "16px 8px",
                          fontWeight: "600",
                          color: "#ffffff",
                        }}
                      >
                        {item.type === "trade" ? item.ticker : "-"}
                      </td>
                      <td style={{ padding: "16px 8px", textAlign: "center" }}>
                        {item.type === "trade" ? (
                          <span
                            style={{
                              display: "inline-flex",
                              alignItems: "center",
                              gap: "4px",
                              padding: "4px 8px",
                              borderRadius: "6px",
                              fontSize: "0.8em",
                              fontWeight: "600",
                              background:
                                item.side === "Long"
                                  ? "rgba(0, 255, 136, 0.2)"
                                  : "rgba(255, 107, 107, 0.2)",
                              color:
                                item.side === "Long" ? "#00ff88" : "#ff6b6b",
                            }}
                          >
                            {item.side === "Long" ? (
                              <TrendingUp size={12} />
                            ) : (
                              <TrendingDown size={12} />
                            )}
                            {item.side}
                          </span>
                        ) : (
                          "-"
                        )}
                      </td>
                      <td
                        style={{
                          padding: "16px 8px",
                          fontSize: "0.9em",
                          maxWidth: "200px",
                          wordWrap: "break-word",
                          whiteSpace: "normal",
                        }}
                      >
                        <div
                          style={{
                            wordWrap: "break-word",
                            whiteSpace: "normal",
                            color: "rgba(255, 255, 255, 0.8)",
                            marginBottom: "4px",
                          }}
                        >
                          {item.type === "trade"
                            ? item.entryReasons
                            : item.comment}
                        </div>
                        {item.type === "trade" && item.comment && (
                          <div
                            style={{
                              fontSize: "0.8em",
                              color: "rgba(255, 255, 255, 0.5)",
                              fontStyle: "italic",
                              wordWrap: "break-word",
                              whiteSpace: "normal",
                              borderTop: "1px solid rgba(255, 255, 255, 0.1)",
                              paddingTop: "8px",
                              marginTop: "8px",
                            }}
                            dangerouslySetInnerHTML={{
                              __html: item.comment.replace(
                                /(https?:\/\/[^\s]+)/g,
                                '<a href="$1" target="_blank" style="color: #40e0d0; text-decoration: underline;">$1</a>'
                              ),
                            }}
                          />
                        )}
                      </td>
                      <td
                        style={{
                          padding: "16px 8px",
                          textAlign: "right",
                          fontWeight: "600",
                          color:
                            item.type === "trade"
                              ? item.result >= 0
                                ? "#00ff88"
                                : "#ff6b6b"
                              : item.amount >= 0
                              ? "#00ff88"
                              : "#ff6b6b",
                        }}
                      >
                        {item.type === "trade"
                          ? `${
                              item.result >= 0 ? "+" : ""
                            }${item.result.toFixed(2)}`
                          : `${item.amount >= 0 ? "+" : "-"}${Math.abs(
                              item.amount
                            ).toFixed(2)}`}
                      </td>
                      <td
                        style={{
                          padding: "16px 8px",
                          textAlign: "right",
                          color: "#ff6b6b",
                        }}
                      >
                        {item.type === "trade"
                          ? `${item.commission.toFixed(2)}`
                          : "-"}
                      </td>
                      <td style={{ padding: "16px 8px", textAlign: "center" }}>
                        {item.type === "trade" ? (
                          <span
                            style={{
                              padding: "4px 8px",
                              borderRadius: "6px",
                              fontSize: "0.8em",
                              fontWeight: "600",
                              background:
                                item.isSystematic === "Да"
                                  ? "rgba(255, 215, 0, 0.2)"
                                  : "rgba(255, 255, 255, 0.1)",
                              color:
                                item.isSystematic === "Да"
                                  ? "#FFD700"
                                  : "rgba(255, 255, 255, 0.7)",
                            }}
                          >
                            {item.isSystematic}
                          </span>
                        ) : (
                          "-"
                        )}
                      </td>
                      <td style={{ padding: "16px 8px", textAlign: "center" }}>
                        <div
                          style={{
                            display: "flex",
                            gap: "8px",
                            justifyContent: "center",
                          }}
                        >
                          <button
                            onClick={() => editTrade(item)}
                            style={{
                              background: "rgba(255, 255, 255, 0.1)",
                              border: "none",
                              borderRadius: "8px",
                              padding: "8px",
                              color: "rgba(255, 255, 255, 0.8)",
                              cursor: "pointer",
                              transition: "all 0.2s ease",
                            }}
                            onMouseOver={(e) => {
                              e.target.style.background =
                                "rgba(255, 255, 255, 0.2)";
                            }}
                            onMouseOut={(e) => {
                              e.target.style.background =
                                "rgba(255, 255, 255, 0.1)";
                            }}
                          >
                            <Edit3 size={16} />
                          </button>
                          <button
                            onClick={() => deleteTrade(item.id)}
                            style={{
                              background: "rgba(255, 107, 107, 0.2)",
                              border: "none",
                              borderRadius: "8px",
                              padding: "8px",
                              color: "#ff6b6b",
                              cursor: "pointer",
                              transition: "all 0.2s ease",
                            }}
                            onMouseOver={(e) => {
                              e.target.style.background =
                                "rgba(255, 107, 107, 0.3)";
                            }}
                            onMouseOut={(e) => {
                              e.target.style.background =
                                "rgba(255, 107, 107, 0.2)";
                            }}
                          >
                            <Trash2 size={16} />
                          </button>
                        </div>
                      </td>
                    </tr>
                  ))}
              </tbody>
            </table>
          </div>
        )}
      </div>
    </div>
  );
};

export default TradingJournal;
