// src/hooks/useFirestore.js
import { useState, useCallback } from "react";
import { doc, setDoc, getDoc, onSnapshot } from "firebase/firestore";
import { db } from "../firebase/config";
import { useAuth } from "./useAuth";

export const useFirestore = () => {
  const { user } = useAuth();
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [firebaseLoaded, setFirebaseLoaded] = useState(false);

  // Проверка доступности Firestore
  const isFirestoreAvailable = useCallback(() => !!db && !!user?.uid, [user]);

  // Сохранение данных пользователя
  const saveUserData = async (data, userId = null) => {
    const targetUserId = userId || user?.uid;
    if (!targetUserId) {
      const errorMsg = "User ID is required for saving data";
      console.error(errorMsg);
      setError(errorMsg);
      return;
    }
    if (!isFirestoreAvailable()) return;

    setLoading(true);
    setError(null);

    try {
      const sanitizedData = Object.fromEntries(
        Object.entries(data).filter(
          ([_, value]) =>
            value !== undefined && value !== null && typeof value !== "function"
        )
      );

      const userDocRef = doc(db, "users", targetUserId);

      // Таймаут операции
      const timeoutPromise = new Promise((_, reject) =>
        setTimeout(() => reject(new Error("Operation timeout")), 10000)
      );

      await Promise.race([
        setDoc(
          userDocRef,
          { ...sanitizedData, lastUpdated: new Date() },
          { merge: true }
        ),
        timeoutPromise,
      ]);

      console.log("Data saved successfully for user:", targetUserId);
    } catch (err) {
      console.error("Error saving user data:", err);
      setError(`Save failed: ${err.message}`);
    } finally {
      setLoading(false);
    }
  };

  // Загрузка данных пользователя
  const loadUserData = async (userId = null) => {
    const targetUserId = userId || user?.uid;
    if (!targetUserId) {
      const errorMsg = "User ID is required for loading data";
      console.error(errorMsg);
      setError(errorMsg);
      return { nickname: "Trader", avatar: "👨‍💼" };
    }
    if (!isFirestoreAvailable()) {
      console.warn("Firestore not available, returning defaults");
      return { nickname: "Trader", avatar: "👨‍💼" };
    }

    setLoading(true);
    setError(null);

    try {
      const userDocRef = doc(db, "users", targetUserId);
      const timeoutPromise = new Promise((_, reject) =>
        setTimeout(() => reject(new Error("Load timeout")), 10000)
      );

      const docSnap = await Promise.race([getDoc(userDocRef), timeoutPromise]);
      setFirebaseLoaded(true);

      if (docSnap.exists()) {
        console.log("Data loaded successfully for user:", targetUserId);
        return docSnap.data();
      } else {
        console.log(
          "No user data found, returning defaults for user:",
          targetUserId
        );
        return { nickname: "Trader", avatar: "👨‍💼" };
      }
    } catch (err) {
      console.error("Error loading user data:", err);
      setError(`Load failed: ${err.message}`);
      setFirebaseLoaded(true);
      return { nickname: "Trader", avatar: "👨‍💼" };
    } finally {
      setLoading(false);
    }
  };

  // Подписка на изменения данных пользователя
  const subscribeToUserData = (callback, userId = null) => {
    const targetUserId = userId || user?.uid;
    if (!targetUserId || typeof callback !== "function") return () => {};

    if (!isFirestoreAvailable()) {
      callback({ nickname: "Trader", avatar: "👨‍💼" });
      return () => {};
    }

    const userDocRef = doc(db, "users", targetUserId);
    console.log("Setting up subscription for user:", targetUserId);

    const unsubscribe = onSnapshot(
      userDocRef,
      (docSnap) => {
        if (docSnap.exists()) callback(docSnap.data());
        else callback({ nickname: "Trader", avatar: "👨‍💼" });
      },
      (err) => {
        console.error("Error in subscription:", err);
        setError(`Subscription error: ${err.message}`);
        callback({ nickname: "Trader", avatar: "👨‍💼" });
      }
    );

    return unsubscribe;
  };

  return {
    loading,
    error,
    firebaseLoaded,
    saveUserData,
    loadUserData,
    subscribeToUserData,
    isFirestoreAvailable,
  };
};
