// src/components/Settings.js
import React, { useState, useEffect } from "react";
import { Upload, User, Trash2, AlertTriangle } from "lucide-react";
import { useAuth } from "../hooks/useAuth";
import { useFirestore } from "../hooks/useFirestore";

const Settings = ({
  balance,
  userProfile: parentUserProfile,
  setUserProfile: setParentUserProfile,
  resetAllData,
}) => {
  const { user, loading: authLoading } = useAuth();
  const { loadUserData, saveUserData } = useFirestore();

  const [userProfile, setUserProfile] = useState({
    avatar: "👨‍💼",
    nickname: "",
  });
  const [tempNickname, setTempNickname] = useState("");
  const [showNicknameEdit, setShowNicknameEdit] = useState(false);
  const [showResetConfirm, setShowResetConfirm] = useState(false);

  // Загрузка данных из Firestore при наличии пользователя
  useEffect(() => {
    if (parentUserProfile) {
      setUserProfile(parentUserProfile);
      setTempNickname(parentUserProfile.nickname || "");
    }
  }, [parentUserProfile]);

  // Сохраняем изменения ника
  const handleNicknameChange = async () => {
    if (!tempNickname.trim()) return;
    try {
      const updatedProfile = { ...userProfile, nickname: tempNickname.trim() };
      setUserProfile(updatedProfile);
      setParentUserProfile(updatedProfile); // Обновляем состояние в App.js
      setShowNicknameEdit(false);
    } catch (error) {
      console.error("Error saving nickname:", error);
    }
  };

  // Сохраняем изменения аватара
  const handleAvatarUpload = (event) => {
    const file = event.target.files[0];
    if (file && file.type.startsWith("image/")) {
      // Проверяем размер файла (максимум 500KB)
      if (file.size > 500000) {
        alert("Image too large. Please select an image smaller than 500KB.");
        return;
      }

      const reader = new FileReader();
      reader.onload = async (e) => {
        try {
          const updatedProfile = { ...userProfile, avatar: e.target.result };
          setUserProfile(updatedProfile);
          setParentUserProfile(updatedProfile); // Обновляем состояние в App.js
        } catch (error) {
          console.error("Error uploading avatar:", error);
          alert("Error uploading image. Please try a smaller image.");
        }
      };
      reader.readAsDataURL(file);
    }
  };

  // Сброс данных пользователя
  const handleResetData = () => {
    resetAllData();
    setShowResetConfirm(false);
  };

  if (authLoading) return <div className="text-white">Loading...</div>;
  if (!user)
    return <div className="text-white">Sign in to access settings</div>;

  const styles = {
    card: {
      background:
        "linear-gradient(135deg, rgba(255, 255, 255, 0.08), rgba(255, 255, 255, 0.02))",
      backdropFilter: "blur(20px)",
      borderRadius: "16px",
      border: "1px solid rgba(255, 255, 255, 0.1)",
      padding: "24px",
    },
    button: {
      padding: "12px 24px",
      borderRadius: "8px",
      border: "none",
      fontWeight: "600",
      cursor: "pointer",
      transition: "all 0.2s ease",
      display: "inline-flex",
      alignItems: "center",
      gap: "8px",
    },
    input: {
      width: "100%",
      padding: "12px",
      background: "rgba(255, 255, 255, 0.1)",
      border: "1px solid rgba(255, 255, 255, 0.2)",
      borderRadius: "8px",
      color: "#ffffff",
      fontSize: "14px",
    },
  };

  return (
    <div className="space-y-6">
      {/* Balance */}
      <div className="flex justify-between items-center mb-8">
        <h1 className="text-4xl font-light text-white">Settings</h1>
        <div className="text-right">
          <div className="text-sm text-white/60">Balance</div>
          <div
            className={`text-2xl font-semibold ${
              balance >= 0 ? "text-green-400" : "text-red-400"
            }`}
          >
            ${balance.toFixed(2)}
          </div>
        </div>
      </div>

      {/* Profile Settings */}
      <div style={styles.card}>
        <h2 className="text-xl font-medium text-white mb-6 flex items-center gap-2">
          <User size={20} />
          Profile Settings
        </h2>

        {/* Avatar Upload */}
        <div className="mb-6 flex items-center gap-4">
          <div className="w-16 h-16 rounded-full bg-white/10 flex items-center justify-center overflow-hidden">
            {typeof userProfile.avatar === "string" &&
            userProfile.avatar.startsWith("data:") ? (
              <img
                src={userProfile.avatar}
                alt="Avatar"
                className="w-full h-full object-cover"
              />
            ) : (
              <span className="text-3xl">{userProfile.avatar}</span>
            )}
          </div>
          <div className="flex flex-col gap-2">
            <input
              type="file"
              accept="image/*"
              id="avatar-upload"
              style={{ display: "none" }}
              onChange={handleAvatarUpload}
            />
            <label
              htmlFor="avatar-upload"
              style={styles.button}
              className="cursor-pointer bg-white/20 text-white"
            >
              <Upload size={16} /> Upload Image
            </label>
          </div>
        </div>

        {/* Nickname */}
        <div className="mb-6">
          <label className="block text-white mb-3 font-medium">Nickname</label>
          {showNicknameEdit ? (
            <div className="flex gap-2">
              <input
                type="text"
                value={tempNickname}
                onChange={(e) => setTempNickname(e.target.value)}
                style={styles.input}
                onKeyPress={(e) => e.key === "Enter" && handleNicknameChange()}
                autoFocus
              />
              <button
                onClick={handleNicknameChange}
                style={{
                  ...styles.button,
                  background: "rgba(0,255,136,0.2)",
                  color: "#00ff88",
                }}
              >
                Save
              </button>
              <button
                onClick={() => {
                  setShowNicknameEdit(false);
                  setTempNickname(userProfile.nickname);
                }}
                style={{
                  ...styles.button,
                  background: "rgba(255,255,255,0.1)",
                  color: "#ffffff",
                }}
              >
                Cancel
              </button>
            </div>
          ) : (
            <div className="flex items-center justify-between p-3 bg-white/5 rounded-lg">
              <span className="text-white">{userProfile.nickname}</span>
              <button
                onClick={() => setShowNicknameEdit(true)}
                style={{
                  ...styles.button,
                  background: "rgba(255,255,255,0.1)",
                  color: "#ffffff",
                  padding: "8px 12px",
                }}
              >
                Edit
              </button>
            </div>
          )}
        </div>
      </div>

      {/* Reset Data */}
      <div style={styles.card}>
        <h2 className="text-xl font-medium text-white mb-6 flex items-center gap-2">
          <Trash2 size={20} />
          Data Management
        </h2>
        <button
          onClick={() => setShowResetConfirm(true)}
          style={{
            ...styles.button,
            background: "rgba(255,107,107,0.2)",
            color: "#ff6b6b",
            border: "1px solid rgba(255,107,107,0.3)",
          }}
        >
          Reset All Data
        </button>
      </div>

      {/* Reset Confirmation Modal */}
      {showResetConfirm && (
        <div
          className="fixed inset-0 flex items-center justify-center bg-black/80 z-50"
          onClick={() => setShowResetConfirm(false)}
        >
          <div
            style={styles.card}
            className="max-w-md w-full p-6"
            onClick={(e) => e.stopPropagation()}
          >
            <div className="flex items-center gap-3 mb-4">
              <AlertTriangle className="w-6 h-6 text-red-400" />
              <h3 className="text-xl font-medium text-white">Confirm Reset</h3>
            </div>
            <p className="text-white/70 mb-6">
              Are you sure you want to reset all data? This cannot be undone.
            </p>
            <div className="flex gap-3 justify-end">
              <button
                onClick={() => setShowResetConfirm(false)}
                style={{
                  ...styles.button,
                  background: "rgba(255,255,255,0.1)",
                  color: "#ffffff",
                }}
              >
                Cancel
              </button>
              <button
                onClick={handleResetData}
                style={{
                  ...styles.button,
                  background: "rgba(255,107,107,0.2)",
                  color: "#ff6b6b",
                  border: "1px solid rgba(255,107,107,0.3)",
                }}
              >
                Yes, Reset
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default Settings;
